import { NgClass } from '@angular/common';
import { ChangeDetectionStrategy, Component, computed, EventEmitter, input, Output, ViewEncapsulation } from '@angular/core';
import { URIconComponent } from '../Icon/ur-icon.component';

export type ButtonSize = 'small' | 'medium' | 'large';
type ButtonShape = 'square' | 'round' | undefined;
type ButtonType = 'text' | 'warning' | 'cta' | 'default' | 'danger';

@Component({
    selector: 'ur-icon-button',
    templateUrl: './ur-icon-button.component.html',
    styleUrls: ['./ur-icon-button.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [NgClass, URIconComponent],
    encapsulation: ViewEncapsulation.None,
})
export class URIconButtonComponent {
    /**
     * Whether the button is filled or not
     * @deprecated - use type instead
     */
    filled = input(false);
    /**
     * Whether to highlight the button as selected/active
     */
    active = input(false);

    /**
     * Name of the icon to use
     */
    icon = input('');

    /**
     * Shape of button: round (circle) or square - (defaults to square).
     * Normally this is not needed as button will get it's form from the type
     * @deprecated - use type instead
     */
    shape = input<ButtonShape>();

    /**
     * Size of button: small, medium or large - (defaults to medium)
     */
    size = input<ButtonSize>('medium');

    /**
     * Type of icon button: text, warning, default, cta - (defaults to 'default')
     * - cta (Primary - round and with interaction background)
     * - text (Secondary - square and without background)
     * - default (Tertiary - round and secondary colors) - default
     * - warning (square and warning colors)
     */
    type = input<ButtonType>('default');

    /**
     * To disable the button
     */
    disabled = input(false);

    /**
     * To make it a ghost button (no background or borders)
     */
    ghost = input(false);

    /**
     * Click event listener
     */
    @Output()
    clicked: EventEmitter<Event> = new EventEmitter();

    /**
     * Click event listener
     * @deprecated - use handleClick instead
     */
    @Output()
    handleClick: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user PRESS the button.
     */
    @Output()
    handlePointerDown: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user RELEASE the button.
     */
    @Output()
    handlePointerUp: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user LEAVE the button.
     */
    @Output()
    handlePointerLeave: EventEmitter<Event> = new EventEmitter();

    private hasBeenClicked = false;
    stateClassNames = computed(() => ({
        [`ur-icon-button-${this.size()}`]: true,
        [`ur-icon-button-${this.type()}`]: !this.ghost(),
        [`ur-icon-button-${this.shape()}`]: this.shape() !== undefined,
    }));

    onClick(event: Event) {
        event.preventDefault();
        this.clicked.emit(event);
        this.handleClick.emit(event);
    }

    onHandlePointerDown(event: Event): void {
        event.preventDefault();
        this.hasBeenClicked = true;
        this.handlePointerDown.emit();
    }

    onHandlePointerUp(event: Event): void {
        event.preventDefault();
        this.hasBeenClicked = false;
        this.handlePointerUp.emit();
    }

    onHandlePointerLeave(event: Event): void {
        event.preventDefault();
        if (this.hasBeenClicked) {
            this.handlePointerLeave.emit();
            this.hasBeenClicked = false;
        }
    }
}
