import { trigger, style, transition, animate, state } from '@angular/animations';
import { ANIMATION_STATE, ANIMATION_TRIGGER } from './models';

export const scaleInOutAnimation = trigger(ANIMATION_TRIGGER.ENTER, [
    transition(':enter', [
        style({ opacity: 0, transform: 'scale(0.7)' }),
        animate('200ms 160ms ease-in', style({ opacity: 1, transform: 'scale(1)' })),
    ]),
    transition(':leave', [animate('50ms ease-in', style({ opacity: 0, transform: 'scale(0.7)' }))]),
]);

export const scaleOnEnterAnimation = trigger(ANIMATION_TRIGGER.ENTER, [
    state(`void, ${ANIMATION_STATE.HIDDEN}`, style({ opacity: 0, transform: 'scale(0.7)' })),
    state(ANIMATION_STATE.VISIBLE, style({ opacity: 1, transform: 'scale(1)' })),
    transition(`* => ${ANIMATION_STATE.VISIBLE}`, [animate('150ms ease-in-out')]),
    transition(`${ANIMATION_STATE.VISIBLE} => *`, [animate('0ms ease-in-out')]),
]);
export const scaleOnPassiveAnimation = trigger(ANIMATION_TRIGGER.PASSIVE, [
    state('active', style({ transform: 'scale(1)' })),
    state('passive', style({ transform: 'scale(0.9)' })),
    transition('active <=> passive', [animate('150ms ease-in')]),
]);
