import { ElementRef } from '@angular/core';
import { getAllowedKeys } from '@universal-robots/ui-models';

//Reset to the min value if the number is empty for not a number.
export const resetValueToMin = (value: string, minValue: number) => {
    if (value.length === 0 || isNaN(Number(value)) || Number(value) === 0) {
        value = String(minValue);
    }

    return value;
};

export const replaceCommaToDot = (value: string): string => {
    if (value.includes(',')) {
        return value.replace(',', '.');
    }
    return value;
};

export const increment = (value: number, ratio: number) => {
    return value + ratio;
};

export const decrement = (value: number, ratio: number) => {
    return value - ratio;
};

export const preventIfNotAllowed = (key: string) => {
    const reg = /\d/;
    const isNum = reg.test(key);
    // Only numbers are allowed and types of AllowedKeys
    return !isNum && !getAllowedKeys().includes(key);
};

export const selectFieldText = (element: ElementRef<HTMLInputElement>): void => {
    element.nativeElement.select();
};
