import { Component, Input, ViewEncapsulation } from '@angular/core';
import { CloseReason, DialogSize } from '@universal-robots/ui-models';
import { scaleOnEnterAnimation, scaleOnPassiveAnimation } from '../../../animations';
import { DialogRef } from '../../../ref';
import { URHeadingComponent } from '../../Heading/ur-heading.component';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { URIconButtonComponent } from '../../IconButton/ur-icon-button.component';
import { URDialog } from '../ur-dialog';

/**
 * A modal is a type of dialog that takes complete priority over the page and prevent the user from interacting with the reset of the application.
 */
@Component({
    selector: 'ur-modal',
    templateUrl: './ur-modal.component.html',
    styleUrls: ['./ur-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: [scaleOnEnterAnimation, scaleOnPassiveAnimation],
    imports: [URIconComponent, URHeadingComponent, URIconButtonComponent],
})
export class URModalComponent extends URDialog {
    @Input()
    override size = DialogSize.XXL;

    constructor(_dialogRef: DialogRef<unknown>) {
        super(_dialogRef);
    }
    /**
     * close dialog on cancel, close-icon and esc
     */
    onClose(event: KeyboardEvent | Event) {
        if (!this.disableClose) {
            event.stopPropagation();
            event.stopImmediatePropagation();

            this.beforeClose.emit({ $event: event, reason: CloseReason.CANCELLED });
            this.dialogRef.close({ reason: CloseReason.CANCELLED });
        }
    }
}
