import { AsyncPipe, DecimalPipe } from '@angular/common';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { from, interval, Observable, Subscription } from 'rxjs';
import { switchMap, takeWhile } from 'rxjs/operators';
import { URIconButtonComponent } from '../IconButton/ur-icon-button.component';

/**
 * Dumb/presentational component.
 * Input stepper UI. Delegates user inputs.
 *
 * Configurable properties:
 * - enabled/disabled
 * - label text
 * - displayed precision
 * - unit
 * - clamping
 *
 * Example template usage:
 *  <ur-range-stepper
 *   (clickedIncrease)="handleSpeedIncrement()"
 *   (clickedDecrease)="handleSpeedDecrement()"
 *   (pressedIncrease)="handleSpeedIncrementJump()"
 *   (pressedDecrease)="handleSpeedDecrementJump()"
 *   [value$]="robotSpeed$"
 *   label="Speed"
 *   precision="2"
 *   unit="%"
 *   min="1"
 *   max="100"
 *   [disabled]="disabled"
 *  ></ur-range-stepper>
 */
@Component({
    selector: 'ur-range-stepper',
    templateUrl: './ur-range-stepper.component.html',
    styleUrls: ['./ur-range-stepper.component.scss'],
    imports: [URIconButtonComponent, AsyncPipe, DecimalPipe],
})
export class URRangeStepperComponent implements OnInit, OnDestroy {
    /**
     * Increase/decrease 'pressed' events stream.
     */
    private readonly interval$: Observable<number> = interval(300).pipe(
        // Use current ranged value to support auto-completion of 'pressed' events stream:
        switchMap(() => this.value$),
    );

    private intervalSubscription?: Subscription;
    private readonly subscriptions: Subscription = new Subscription();

    /**
     * Whether the control is disabled or not.
     */
    @Input()
    disabled = false;

    /**
     * Current ranged value.
     */
    @Input()
    value$: Observable<number> = from([100]);

    /**
     * Displayed label.
     */
    @Input()
    label?: string;

    /**
     * Number of decimal digits to be used when displaying the ranged value.
     */
    @Input()
    precision = 0;

    /**
     * The unit type to be used when displaying the ranged value, e.g. '%', 'Meters', etc.
     */
    @Input()
    unit = '';

    /**
     * Clamp
     */
    @Input()
    min = 1;

    /**
     * Clamp
     */
    @Input()
    max = 100;

    /**
     * Value change request event emitter.
     * Should be called when the user CLICKS the decrease button.
     */
    @Output()
    clickedDecrease: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user CLICKS the increase button.
     */
    @Output()
    clickedIncrease: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user HOLDS DOWN the decrease button.
     */
    @Output()
    pressedDecrease: EventEmitter<Event> = new EventEmitter();

    /**
     * Value change request event emitter.
     * Should be called when the user HOLDS DOWN the increase button.
     */
    @Output()
    pressedIncrease: EventEmitter<Event> = new EventEmitter();

    public value = 100;

    ngOnInit() {
        // Store value state locally, for clamp logic usage (template):
        this.subscriptions.add(this.value$.subscribe((value) => (this.value = value)));
    }

    ngOnDestroy() {
        this.subscriptions.unsubscribe();
        this.intervalSubscription?.unsubscribe();
    }

    /**
     * Initiates a stream of 'decrease' events.
     */
    public onPointerDownDecrease(e: Event) {
        e.preventDefault();

        if (!this.disabled) {
            // Clean up any ongoing event stream, before starting a new one:
            this.intervalSubscription?.unsubscribe();

            this.intervalSubscription = this.interval$
                .pipe(
                    // In case of a missed unsubscribe, auto-complete the stream:
                    takeWhile((value) => value > this.min && value <= this.max),
                )
                .subscribe(() => {
                    this.pressedDecrease.emit();
                });
        }
    }

    /**
     * Initiates a stream of 'increase' events.
     */
    public onPointerDownIncrease(e: Event) {
        e.preventDefault();

        if (!this.disabled) {
            // Clean up any ongoing event stream, before starting a new one:
            this.intervalSubscription?.unsubscribe();

            this.intervalSubscription = this.interval$
                .pipe(
                    // In case of a missed unsubscribe, auto-complete the stream:
                    takeWhile((value) => value >= this.min && value < this.max),
                )
                .subscribe(() => {
                    this.pressedIncrease.emit();
                });
        }
    }

    /**
     * Ends any stream of 'decrease/increase' events.
     */
    public onPointerLeave() {
        this.intervalSubscription?.unsubscribe();
    }

    /**
     * Constructs the digitsInfo parameter for the DecimalPipe.
     */
    public getDigitsInfo(decimals: number): string {
        return `1.${decimals}-${decimals}`;
    }
}
