import { FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    Host,
    OnDestroy,
    OnInit,
    ViewEncapsulation,
} from '@angular/core';
import { Subscription, merge } from 'rxjs';
import { filter } from 'rxjs/operators';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { urExpansionAnimations } from '../expansion-animations';
import { URExpansionPanelComponent } from '../ur-expansion-panel/ur-expansion-panel.component';

@Component({
    selector: 'ur-expansion-panel-header',
    templateUrl: './ur-expansion-panel-header.component.html',
    styleUrls: ['../ur-expansion-panel-shared.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    animations: [urExpansionAnimations.indicatorRotateInverse, urExpansionAnimations.subTitleExpansion],
    imports: [URIconComponent],
})
export class URExpansionPanelHeaderComponent implements OnInit, OnDestroy {
    private _parentChangeSubscription = Subscription.EMPTY;

    constructor(
        @Host() public panel: URExpansionPanelComponent,
        protected readonly _element: ElementRef,
        protected readonly _focusMonitor: FocusMonitor,
        protected readonly _changeDetectorRef: ChangeDetectorRef,
    ) {}

    /** Whether the associated panel is disabled. */
    get disabled(): boolean {
        return this.panel.disabled;
    }

    ngOnInit(): void {
        this._parentChangeSubscription = merge(
            this.panel.opened,
            this.panel.closed,
            this.panel._inputChanges.pipe(
                filter((changes) => {
                    return !!(changes['hideToggle'] || changes['disabled']);
                }),
            ),
        ).subscribe(() => this._changeDetectorRef.markForCheck());

        // Avoids focus being lost if the panel contained the focused element and was closed.
        this.panel.closed
            .pipe(filter(() => this.panel._containsFocus()))
            .subscribe(() => this._focusMonitor.focusVia(this._element, 'program'));
    }

    ngOnDestroy(): void {
        this._parentChangeSubscription.unsubscribe();
    }

    /** Toggles the expanded state of the panel. */
    _toggle(_event: Event): void {
        if (!this.disabled) {
            this.panel.toggle();
        }
    }

    /** Gets whether the panel is expanded. */
    _isExpanded(): boolean {
        return this.panel.expanded;
    }

    /** Gets the expanded state string of the panel. */
    _getExpandedState(): string {
        return this.panel._getExpandedState();
    }

    /** Gets the panel id. */
    _getPanelId(): string {
        return this.panel.id;
    }

    /** Gets whether the expand indicator should be shown. */
    _showToggle(): boolean {
        return !this.panel.hideToggle && !this.panel.disabled;
    }

    /**
     * Focuses the panel header. Implemented as a part of `FocusableOption`.
     * @param origin Origin of the action that triggered the focus.
     * @param options Focus options
     * @docs-private
     */
    focus(origin?: FocusOrigin, options?: FocusOptions) {
        if (origin) {
            this._focusMonitor.focusVia(this._element, origin, options);
        } else {
            this._element.nativeElement.focus(options);
        }
    }
}
