import { Component, EventEmitter, forwardRef, Input, Output, ViewChild } from '@angular/core';
import { URListComponent } from '../List/ur-list.component';
import { URPopoverContentComponent } from '../Popover/PopoverContent/ur-popover-content.component';
import { URPopoverDirective } from '../Popover/ur-popover.directive';
import { URTitleComponent } from '../Title/ur-title.component';

/**
 * @deprecated
 * DEPRECATED: Please use the ur-dropdown component instead
 */

@Component({
    selector: 'ur-list-popover',
    styleUrls: ['ur-list-popover.component.scss'],
    templateUrl: 'ur-list-popover.component.html',
    imports: [URPopoverContentComponent, URTitleComponent, URListComponent, URPopoverDirective],
})
export class URListPopoverComponent {
    @ViewChild(forwardRef(() => URPopoverDirective))
    popover?: URPopoverDirective;

    /**
     *  Popover heading text
     */
    @Input()
    heading?: string;

    /**
     * The options to show in the list
     * Either an array of strings, or an object with key:value pairs
     * For arrays the option is what is displayed and used as the value
     * For objects the key is what is emitted on selectionChange and the value is the displayed option text
     */
    @Input()
    options?: Array<string> | { [key: string]: string };

    /**
     * Optional list of disabled options in the list
     */
    @Input()
    disabledOptions: Array<string> = [];

    /**
     * The currently selected value
     */
    @Input()
    value?: string;

    /**
     * Whether the popover should close when a selection is made
     */
    @Input()
    closeOnSelection = false;

    /**
     * flag to force show or hide the popover
     */
    @Input()
    visible = false;

    @Input()
    disabled = false;

    /**
     * Event emitted when the selection changes
     */
    @Output()
    selectionChange = new EventEmitter<string>();

    /**
     * Event emitted when an option is clicked
     */
    @Output()
    pick = new EventEmitter<string>();

    setValue(option: string) {
        if (option !== this.value) {
            this.value = option;
            this.selectionChange.emit(option);
        }
        this.pick.emit(option);
        if (this.closeOnSelection) {
            this.popover?.hide();
        }
    }

    optionsIsArray(): boolean {
        return Array.isArray(this.options);
    }

    getOptions() {
        if (!this.options) {
            return [];
        }
        return this.optionsIsArray() ? this.options : Object.keys(this.options);
    }

    getOptionText(option: string) {
        return this.optionsIsArray() ? option : (this.options as { [key: string]: string })[option];
    }
}
