import { OverlayContainer } from '@angular/cdk/overlay';
import { Injectable } from '@angular/core';

@Injectable()
export class UROverlayContainer extends OverlayContainer {
    protected override _createContainer(): void {
        const containerClass = 'ur-overlay-container';

        let container = this._document.querySelector(`.${containerClass}`);

        if (!container) {
            container = this._document.createElement('div');
            container.classList.add(containerClass);
            this._document.body.appendChild(container);
        }

        this._containerElement = container as HTMLElement;
    }
}
