import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Output,
    ViewChild,
    ViewEncapsulation,
    computed,
    input,
} from '@angular/core';
import { Guid } from 'guid-typescript';
import { URIconComponent } from '../../Icon/ur-icon.component';

/**
 * Ur-field-trigger is a shadow of the ur input field and have the responsibility to capture a click and forward it
 * please notice: on invalidity it will use the warning state indicating that the user should act in order to turn it valid
 */

@Component({
    selector: 'ur-field-trigger',
    templateUrl: './ur-field-trigger.component.html',
    styleUrls: ['./ur-field-trigger.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URIconComponent],
})
export class URFieldTriggerComponent {
    @ViewChild('button') button?: ElementRef<HTMLButtonElement>;
    /**
     * unique ID
     */

    uid = input(Guid.raw());

    /**
     * value to be shown, has to be bound to a value
     */

    value = input('');

    /**
     * placeholder if the field doesn't have any value
     */

    placeholder = input('');

    /**
     * unit
     */

    unit = input('');

    /**
     * error message to show if character length is exceeded
     */
    valid = input(true);

    /**
     * whether the input is in warning state or not
     */
    isWarning = input(false);

    /**
     * label
     */

    label = input('');

    /**
     * disabled state
     */
    disabled = input(false);

    /**
     * if input is of type password
     */

    isPassword = input(false);

    /**
     * required state
     */
    required = input(false);
    empty = computed(() => {
        return this.value()?.toString().length === 0;
    });

    /**
     * click event handler
     * @deprecated use clicked instead
     */
    @Output()
    handleClick: EventEmitter<Event> = new EventEmitter();
    /**
     * click event handler
     */
    @Output()
    clicked: EventEmitter<Event> = new EventEmitter();

    displayValue = computed(() => {
        if (this.value()?.toString().length > 0 && !this.isPassword()) {
            return this.value();
        } else if (this.value()?.toString().length > 0 && this.isPassword()) {
            return [...this.value()].map(() => '*').join(' ');
        } else {
            return this.placeholder();
        }
    });

    onClick($event: Event) {
        this.handleClick.emit($event);
        this.clicked.emit($event);
    }

    setFocus() {
        this.button?.nativeElement.focus();
    }
}
