import { NgClass } from '@angular/common';
import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { createPopper, Placement, Instance as PopperInstance, Options as PopperOptions } from '@popperjs/core';
import { URPopoverContentType } from '../ur-popover-content-type';

@Component({
    selector: 'ur-popover-content',
    templateUrl: './ur-popover-content.component.html',
    styleUrls: ['./ur-popover-content.component.scss'],
    imports: [NgClass],
})
export class URPopoverContentComponent {
    @ViewChild('popoverViewRef')
    popoverViewRef?: ElementRef;
    URPopoverContentType: typeof URPopoverContentType = URPopoverContentType;

    @Input()
    contentType: URPopoverContentType = URPopoverContentType.DEFAULT;

    /**
     * flag to whether show the arrow or not
     */
    @Input()
    showArrow = true;

    /**
     * container vertical position offset
     */
    @Input()
    verticalOffset = 0;

    /**
     * container horizontal position offset
     */
    @Input()
    horizontalOffset = 16;

    /**
     * width of the container
     */
    @Input()
    width = 214;

    @Input()
    placement: Placement = 'top';

    /**
     * popover close event listener
     */
    @Output()
    closed: EventEmitter<Event> = new EventEmitter();

    /**
     * popover open event listener
     */
    @Output()
    opened: EventEmitter<Event> = new EventEmitter();

    displayType = 'none';
    anchorElement?: HTMLElement;
    popperInstance?: PopperInstance;

    private _popoverData: any;
    @Input()
    set popoverData(value: any) {
        this._popoverData = value;
        this._cd.markForCheck();
        this._cd.detectChanges();
    }

    get popoverData(): any {
        return this._popoverData;
    }

    constructor(
        public _elementRef: ElementRef,
        private readonly _cd: ChangeDetectorRef,
    ) {}

    show(): void {
        if (!this.anchorElement) {
            return;
        }

        const options: Partial<PopperOptions> = {
            placement: this.placement,
            strategy: 'fixed',
            modifiers: [
                {
                    name: 'offset',
                    options: {
                        offset: [this.verticalOffset, this.horizontalOffset],
                    },
                },
                {
                    name: 'computeStyles',
                    options: {
                        gpuAcceleration: false,
                    },
                },
            ],
        };

        if (this.popoverViewRef) {
            this.popperInstance = createPopper(this.anchorElement, this.popoverViewRef.nativeElement, options);
            // Make the div focus-able to allow it to act as the focus-trap
            this.popoverViewRef.nativeElement.tabIndex = -1;
        }
        this.toggleVisibility(true);
    }

    hide() {
        this.popperInstance?.destroy();
        this.toggleVisibility(false);
    }

    toggleVisibility(state = false) {
        if (!state) {
            this.displayType = 'none';
            this.closed.emit();
        } else {
            this.displayType = 'block';
            this.opened.emit();
        }
        this._cd.detectChanges();
    }

    update() {
        this.popperInstance?.update();
    }
}
