import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { URIconComponent } from '../Icon/ur-icon.component';

export type CheckboxLabelPosition = 'before' | 'after';
@Component({
    selector: 'ur-checkbox',
    templateUrl: 'ur-checkbox.component.html',
    styleUrls: ['ur-checkbox.component.scss'],
    encapsulation: ViewEncapsulation.None,
    imports: [URIconComponent],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class URCheckboxComponent {
    /*
     * Position the label 'before' or 'after'. Use the type CheckboxLabelPosition
     * */
    @Input() labelPos: CheckboxLabelPosition = 'after';
    /*
     * The text associated with the input field.
     * */
    @Input() label = '';

    /*
     * Determines if the checkbox is set or not.
     * */
    @Input() checked = false;

    /*
     * This prop is used when you submit in a form.
     * */
    @Input() name?: string;

    /*
     * Determines if the component is disabled or not.
     * */
    @Input() disabled = false;

    /*
     *  Determines if the hover event should be enabled or not.
     * */
    @Input() noHover = false;

    /*
     * Event to call outside the component - used to invoke methods on the parent component.
     * */
    @Output() handleChange = new EventEmitter<boolean>();

    /*
     * EventHandler for when the native input field is updating.
     * */
    onChange(event: Event) {
        if (this.disabled) {
            event.preventDefault();
            return;
        }
        this.checked = !this.checked;
        this.handleChange.emit(this.checked);
    }
}
