import { OverlayRef } from '@angular/cdk/overlay';
import { Inject, Injectable } from '@angular/core';
import { FocusTrap } from 'focus-trap';
import { Subject, Observable, Subscription } from 'rxjs';
import { CloseDialog, CloseReason, DIALOG_KEYS, DialogConfig, DialogError } from '@universal-robots/ui-models';
import { ANIMATION_STATE, ANIMATION_TRIGGER, DialogAnimationEvent, DialogAnimationState } from '../animations';
import { LocalStorageList } from '../utils/local-storage';

export interface DialogRefInterface {
    cleanUp: () => void;
}

@Injectable()
export class DialogRef<T> implements DialogRefInterface {
    disableClose = false;
    private localStorageHelper = new LocalStorageList<string>(DIALOG_KEYS.LOCALSTORAGE);
    private isAnimatedPassive = false;
    private animationState$ = new Subject<DialogAnimationState>();
    private afterClose$ = new Subject<CloseDialog<T>>();
    private afterOpen$ = new Subject<string>();
    private afterCleanUp$ = new Subject<string>();
    private subscriptions$ = new Subscription();
    private _focusTrap?: FocusTrap;
    constructor(
        private readonly overlayRef: OverlayRef,
        @Inject(String) public readonly id: string,
        @Inject(String) public readonly config: DialogConfig,
    ) {
        this.subscriptions$.add(
            this.afterClose.subscribe({
                complete: () => {
                    this.animationState$.next({ animation: ANIMATION_TRIGGER.ENTER, state: ANIMATION_STATE.HIDDEN });
                },
            }),
        );
        this.subscriptions$.add(this.animationState.subscribe(this.onAnimationStateChange));
        this.subscriptions$.add(this.overlayRef.backdropClick().subscribe(this.onBackdropClick));
        (window as any).addEventListener(DIALOG_KEYS.UPDATE_EVENT, this.animationEffect);
        (window as any).addEventListener(DIALOG_KEYS.CLOSE_EVENT, this.forceClose);
        (window as any).addEventListener(DIALOG_KEYS.CLOSE_BY_ID_EVENT, this.closeById);
        // (window as any).addEventListener(DIALOG_KEYS.CLOSE_CRITICAL_EVENT, this.forceCloseAllCritical);
        (window as any).addEventListener(DIALOG_KEYS.CLOSE_BY_ERROR_TYPE_EVENT, this.closeByErrorType);
    }
    /** Gets an observable that emits when dialog is finished closing. */
    get afterClose(): Observable<CloseDialog<T>> {
        return this.afterClose$.asObservable();
    }
    /** Gets an observable that emits when dialog is finished opening. */
    get afterOpen(): Observable<string> {
        return this.afterOpen$.asObservable();
    }
    /** Gets an observable that emits when overlay has been attached. */
    get animationState(): Observable<DialogAnimationState> {
        return this.animationState$.asObservable();
    }
    get afterCleanUp(): Observable<string> {
        return this.afterCleanUp$.asObservable();
    }
    get overlayRefElement(): HTMLElement {
        return this.overlayRef.overlayElement;
    }
    get focusTrap(): FocusTrap | undefined {
        return this._focusTrap;
    }

    public setAnimationState(state: DialogAnimationState) {
        this.animationState$.next(state);
    }

    public close(data?: CloseDialog<T>): void {
        const returnData = data ?? { reason: CloseReason.UNKNOWN };
        this.afterClose$.next(returnData);
        this.afterClose$.complete();
    }
    /**
     * Clean up after the dialog has been removed from the DOM.
     */
    public cleanUp() {
        // Remove the dialog from the DOM after the animation has finished

        this.overlayRef?.backdropElement?.addEventListener(
            'transitionend',
            () => {
                this.overlayRef.dispose();
            },
            { once: true },
        );
        if (this.id) {
            // Remove this dialog from local storage
            this.localStorageHelper.remove(this.id);

            // If multiple dialogs are open, the last one should be re-activated
            (window as any).dispatchEvent(
                new CustomEvent<DialogAnimationEvent>(DIALOG_KEYS.UPDATE_EVENT, {
                    detail: { animation: ANIMATION_TRIGGER.ENTER, state: ANIMATION_STATE.HIDDEN, activeDialogId: this.id },
                }),
            );
            this.afterCleanUp$.next(this.id);
            this.afterCleanUp$.complete();
        }

        this.overlayRef.detach();
        (window as any).removeEventListener(DIALOG_KEYS.UPDATE_EVENT, this.animationEffect);
        (window as any).removeEventListener(DIALOG_KEYS.CLOSE_EVENT, this.forceClose);
        (window as any).removeEventListener(DIALOG_KEYS.CLOSE_BY_ID_EVENT, this.closeById);
        (window as any).removeEventListener(DIALOG_KEYS.CLOSE_BY_ERROR_TYPE_EVENT, this.closeByErrorType);
        this.subscriptions$.unsubscribe();
    }

    private readonly animationEffect = (event: CustomEvent<DialogAnimationEvent>) => {
        const isDialogLastInList = this.id === this.localStorageHelper.getLatest();

        // Dialog becomes active
        if (event.detail.state === ANIMATION_STATE.VISIBLE && event.detail.activeDialogId === this.id) {
            this.animationState$.next({ animation: ANIMATION_TRIGGER.ENTER, state: ANIMATION_STATE.VISIBLE });
        }
        // Another dialog becomes active, current dialog goes passive or removes itself
        else if (event.detail.state === ANIMATION_STATE.VISIBLE && event.detail.activeDialogId !== this.id) {
            if (this.config.maxOpenDialogs && this.localStorageHelper.length() > this.config.maxOpenDialogs) {
                this.close({ reason: CloseReason.DESTROYED });
                return;
            }
            this.overlayRef.backdropElement?.classList.add('is-passive');
            this.animationState$.next({ animation: ANIMATION_TRIGGER.PASSIVE, state: ANIMATION_STATE.PASSIVE });
            this.isAnimatedPassive = true;
        }
        // Reactivate the passive dialog when the last dialog is closed
        else if (event.detail.state === ANIMATION_STATE.HIDDEN && event.detail.activeDialogId !== this.id && isDialogLastInList) {
            if (this.isAnimatedPassive) {
                this.overlayRef.backdropElement?.classList.remove('is-passive');
                this.animationState$.next({ animation: ANIMATION_TRIGGER.PASSIVE, state: ANIMATION_STATE.ACTIVE });
            }
        }
    };
    private onAnimationStateChange = (animationState: DialogAnimationState) => {
        const { animation, state, $event } = animationState;
        if (animation === ANIMATION_TRIGGER.ENTER && state === ANIMATION_STATE.VISIBLE && $event?.toState === ANIMATION_STATE.VISIBLE) {
            // Enable focus trap after dialog has been added to the DOM
            this.afterOpen$.next(this.id);
            this.afterOpen$.complete();
        } else if (animation === ANIMATION_TRIGGER.PASSIVE && state === ANIMATION_STATE.ACTIVE) {
            // Activate focus trap when dialog is away from passive
        } else if (
            animation === ANIMATION_TRIGGER.ENTER &&
            state === ANIMATION_STATE.HIDDEN &&
            $event?.toState === ANIMATION_STATE.HIDDEN
        ) {
            // Clean up after dialog has been removed from the DOM
            this.cleanUp();
        }
    };
    /**
     * Force close the dialog callback to be used in eventlistener
     * @param event
     * @returns
     */
    private readonly forceClose = (event: CustomEvent): void => {
        if (this.config.critical || this.config.error) {
            return;
        }

        if (event.detail?.customData && event.detail?.id === this.id) {
            this.close(event.detail.customData);
            return;
        }

        this.close({ reason: CloseReason.DESTROYED });
    };

    private readonly closeByErrorType = (event: CustomEvent<DialogError>): void => {
        const configError = this.config.error;
        const eventError = event.detail;

        if (!configError || !eventError) {
            return;
        }
        if (eventError.errorType === 'SAFETY_MODE') {
            this.handleSafetyModeErrorDialogs(eventError, configError);
        }
    };
    private handleSafetyModeErrorDialogs(eventError: DialogError, configError: DialogError): void {
        if (configError.errorCodes) {
            const errorCodeMatch = eventError?.errorCodes?.some((code) => configError.errorCodes?.includes(code));
            if (eventError.errorType === configError.errorType && errorCodeMatch) {
                this.close({ reason: CloseReason.DESTROYED });
            }
        }
    }
    private readonly closeById = (event: CustomEvent): void => {
        if (event.detail?.id === this.id) {
            this.close({ reason: CloseReason.DESTROYED });
        }
    };
    /**
     * Close the dialog when the backdrop is clicked
     */
    private readonly onBackdropClick = () => {
        const canClose = !this.config.critical && !this.config.disableClose && !this.disableClose;
        if (canClose) {
            this.close({ reason: CloseReason.CANCELLED });
        }
    };
}
