import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';

@Component({
    selector: 'ur-toggle-switch',
    templateUrl: './ur-toggle-switch.component.html',
    styleUrls: ['./ur-toggle-switch.component.scss'],
    encapsulation: ViewEncapsulation.None,
    standalone: true,
})
export class URToggleSwitchComponent {
    //Private variables
    private _toggled = false;
    private _disabled = false;
    private _labelPos: 'before' | 'after' = 'after';
    private _hover = false;
    private _pressed = false;
    private _orientation: 'row' | 'column' = 'row';

    /**
     * Whether the switch should be disabled
     */
    @Input()
    get disabled(): boolean {
        return this._disabled;
    }

    set disabled(value: boolean) {
        if (this._disabled !== value) {
            this._disabled = value;
        }
    }

    /**
     * whether to highlight the toggle switch as checked
     */
    @Input()
    get toggled(): boolean {
        return this._toggled;
    }

    set toggled(value: boolean) {
        this._toggled = value;
    }

    /*
     * Whether the switch can be hovered
     * */
    @Input()
    get hover(): boolean {
        return this._hover;
    }

    set hover(value: boolean) {
        if (this._hover !== value) {
            this._hover = value;
        }
    }

    /**
     * Sets the label
     * */
    @Input()
    label = '';

    /** Used to set the 'aria-label' attribute on the underlying input element. */
    @Input('aria-label')
    ariaLabel?: string;

    /** The 'aria-labelledby' attribute takes precedence as the element's text alternative. */
    @Input('aria-labelledby')
    ariaLabelledby?: string;

    /** The 'aria-describedby' attribute is read after the element's label and field type. */
    @Input('aria-describedby')
    ariaDescribedby?: string;

    /**
     * Determines the label position
     * */
    @Input()
    get labelPos(): 'before' | 'after' {
        return this._labelPos;
    }

    set labelPos(value: 'before' | 'after') {
        this._labelPos = value;
    }

    @Input()
    get labelOrientation(): 'row' | 'column' {
        return this._orientation;
    }

    set labelOrientation(value: 'row' | 'column') {
        if (this._orientation !== value) {
            this._orientation = value;
        }
    }

    get pressed() {
        return this._pressed;
    }

    set pressed(value: boolean) {
        this._pressed = value;
    }

    /**
     * Handle event emitter
     */
    @Output()
    handleToggle: EventEmitter<Event> = new EventEmitter();

    handlePointerDown(event: PointerEvent) {
        this.pressed = true;
    }

    handlePointerUp(event: PointerEvent) {
        this.pressed = false;
    }

    handlePointerLeave(event: PointerEvent) {
        this.pressed = false;
    }
}
