import { BasePortalOutlet, CdkPortalOutlet, ComponentPortal, PortalModule, TemplatePortal } from '@angular/cdk/portal';
import {
    ChangeDetectionStrategy,
    Component,
    ComponentRef,
    ElementRef,
    EmbeddedViewRef,
    NgZone,
    OnDestroy,
    ViewChild,
    ViewEncapsulation,
    inject,
} from '@angular/core';
import { createFocusTrap, FocusTrap } from 'focus-trap';
import { first } from 'rxjs';
import { DIALOG_KEYS, DialogConfig } from '@universal-robots/ui-models';
import { ANIMATION_STATE, DialogAnimationEvent } from '../../animations';
import { CONFIG_TOKEN, DialogRef } from '../../ref';
import { LocalStorageList } from '../../utils/local-storage';

export function throwDialogContentAlreadyAttachedError() {
    throw Error('Attempting to attach dialog content after content is already attached');
}

/**
 * Internal component that wraps user-provided dialog content.
 * @docs-private
 */
@Component({
    selector: 'ur-dialog-container',
    template: ` <ng-template cdkPortalOutlet #portal />`,
    encapsulation: ViewEncapsulation.None,
    // Using OnPush for dialogs caused some sync issues.
    // tslint:disable-next-line:validate-decorators
    changeDetection: ChangeDetectionStrategy.Default,
    imports: [PortalModule],
    host: {
        class: 'ur-dialog-container-wrapper',
        tabindex: '-1',
    },
    standalone: true,
})
export class URDialogContainerComponent<C extends DialogConfig = DialogConfig> extends BasePortalOutlet implements OnDestroy {
    private readonly localStorageHelper = new LocalStorageList<string>(DIALOG_KEYS.LOCALSTORAGE);
    protected elementRef = inject(ElementRef);
    protected config = inject(CONFIG_TOKEN);
    protected dialogRef = inject(DialogRef);

    protected _ngZone = inject(NgZone);

    /** The portal outlet inside of this container into which the dialog content will be loaded.
     * need to be static because the content is loaded in the constructor
     * and the view is not initialized yet.
     */
    @ViewChild(CdkPortalOutlet, { static: true }) portalOutlet!: CdkPortalOutlet;

    /**
     * Focus trap that will be used to trap and manage focus within the dialog.
     */
    private focusTrap?: FocusTrap;

    constructor() {
        super();
        (window as any).addEventListener(DIALOG_KEYS.UPDATE_EVENT, this.animationEffect);
    }
    animationEffect = (event: CustomEvent<DialogAnimationEvent>) => {
        const isDialogLastInList = this.dialogRef.id === this.localStorageHelper.getLatest();
        const reactiveLastDialog =
            event.detail.state === ANIMATION_STATE.HIDDEN && event.detail.activeDialogId !== this.dialogRef.id && isDialogLastInList;
        // Another dialog becomes active, current dialog goes passive or removes itself
        if (event.detail.state === ANIMATION_STATE.VISIBLE && event.detail.activeDialogId !== this.dialogRef.id) {
            this.pauseFocusTrap();
        }
        // Reactivate the passive dialog when the last dialog is closed
        else if (reactiveLastDialog) {
            this.reActivateFocusTrap();
        }
    };
    /** */
    protected contentAttached() {
        this.initializeFocusTrap();
    }

    /**
     * Can be used by child classes to customize the initial focus
     * capturing behavior (e.g. if it's tied to an animation).
     */
    protected activateFocusTrap() {
        this.focusTrap?.activate();
    }
    /**
     * Pauses the focus trap. This is used when multiple dialogs are open and
     * one of them is not the active dialog. The focus trap will be paused
     * until the dialog is activated again.
     */
    protected pauseFocusTrap() {
        this.focusTrap?.pause();
    }
    /**
     * Recaptures the focus trap. This is used when the dialog is activated again
     * after being paused.
     */
    protected reActivateFocusTrap() {
        this.focusTrap?.unpause();
    }
    ngOnDestroy() {
        this.focusTrap?.deactivate();
        (window as any).removeEventListener(DIALOG_KEYS.UPDATE_EVENT, this.animationEffect);
    }

    /**
     * Attach a ComponentPortal as content to this dialog container.
     * @param portal Portal to be attached as the dialog content.
     */
    attachComponentPortal<T>(portal: ComponentPortal<T>): ComponentRef<T> {
        if (this.portalOutlet.hasAttached()) {
            throwDialogContentAlreadyAttachedError();
        }

        const result = this.portalOutlet.attachComponentPortal(portal);
        this.contentAttached();
        return result;
    }

    /**
     * Attach a TemplatePortal as content to this dialog container.
     * @param portal Portal to be attached as the dialog content.
     */
    attachTemplatePortal<T>(portal: TemplatePortal<T>): EmbeddedViewRef<T> {
        if (this.portalOutlet.hasAttached()) {
            throwDialogContentAlreadyAttachedError();
        }

        const result = this.portalOutlet.attachTemplatePortal(portal);
        this.contentAttached();
        return result;
    }

    /** Sets up the focus trap. */
    private initializeFocusTrap() {
        this._ngZone.onStable.pipe(first()).subscribe(() => {
            this._ngZone.runOutsideAngular(() => {
                this.focusTrap = createFocusTrap(this.elementRef.nativeElement, {
                    trapStack: (window as any)?.__trapStack,
                    escapeDeactivates: false,
                    allowOutsideClick: this.allowOutsideClick,
                });
                this.activateFocusTrap();
            });
        });
    }
    private readonly allowOutsideClick = (e: MouseEvent | TouchEvent) => {
        const isClosestElementAllowed = (element: HTMLElement, selectors: string[]): boolean => {
            return selectors.some((selector) => element.closest(selector));
        };
        const element = e.target as HTMLElement;
        const allowedSelectors = [
            'ur-icon-button.close-button',
            'ur-virtual-keyboard',
            '.ng-option',
            'ur-freedrive-popup',
            '.ur-overlay-backdrop',
        ];
        return isClosestElementAllowed(element, allowedSelectors);
    };
}
