import {
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    HostBinding,
    Input,
    OnDestroy,
    Output,
    ViewChild,
    ViewEncapsulation,
    input,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { DropdownPosition, NgSelectComponent, NgSelectConfig, NgSelectModule } from '@ng-select/ng-select';
import { TranslateService } from '@ngx-translate/core';
import { distinctUntilChanged, firstValueFrom, fromEvent, Subscription, debounceTime, filter, merge } from 'rxjs';
import {
    CloseReason,
    DIALOG_KEYS,
    DialogInputData,
    DialogInputReturnData,
    DropdownOption,
    InputValidator,
} from '@universal-robots/ui-models';
import { URPortalService } from '../../services/ur-portal.service';
import { uniqueAlphaNumericId } from '../../utils/uniqueAlphaNumericId';
import { URDialogInputContainerComponent } from '../Field/DialogInputContainer/ur-dialog-input-container.component';
import { URIconComponent } from '../Icon/ur-icon.component';
import { URLabelComponent } from '../Label/ur-label.component';

@Component({
    selector: 'ur-dropdown',
    templateUrl: './ur-dropdown.component.html',
    styleUrls: ['./ur-dropdown.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [NgSelectModule, FormsModule, URIconComponent, URLabelComponent],
})
export class URDropdownComponent implements OnDestroy {
    private readonly subscriptions: Subscription = new Subscription();
    isKeyboard = false;
    isOpen = false;
    isFocusVisible = false;
    panelContainer: HTMLElement | null = null;
    uuid = uniqueAlphaNumericId();

    /**
     * items visible in the list
     */
    @Input()
    numberOfOptionsVisible = 4;

    /**
     * selected item
     */
    @Input()
    selectedOption: unknown | DropdownOption;

    /**
     * disable the dropdown
     */
    @Input()
    disabled = false;

    /**
     * list of items to be shown in the dropdown. Should be an array of either strings or object's.
     */
    @Input()
    options?: unknown[] | DropdownOption[];

    /**
     * enable addition of items (default: false).
     */
    @Input()
    enableAddition = false;

    /**
     * dropdown label
     */
    @Input()
    label?: string;

    /**
     * label generator function that takes the value as an argument and returns a string label
     */
    @Input()
    labelFactory?: (value: any) => string;

    /**
     * dropdown label
     */
    @Input()
    labelOutside = false;

    /**
     * makes the dropdown searchable, this will filter the dropdown content. this feature is currently disabled (default: false)
     * @disabled
     */
    @Input()
    searchable = false;

    /**
     * enable the possibility to clear the selected item. this feature is currently disabled (default: false).
     * @disabled
     */
    @Input()
    clearable = false;

    /**
     * bindLabel
     */
    @Input()
    bindLabel?: string;

    /**
     * bindValue - binds to the given models value key.
     */
    @Input()
    bindValue?: string;

    /**
     * validators for the input field when creating a new item
     */
    @Input()
    validators: InputValidator[] = [];

    /**
     * whether a selected option is mandatory
     */
    @Input()
    required = true;

    /**
     * Determines if the dropdown is able to add new item to the list
     * */
    @Input()
    allowsOptionCreation = false;

    /**
     * value for new item dialog
     */
    @Input()
    createValue = '';

    /**
     * Group options into sections
     */
    @Input()
    groupBy?: string;

    /**
     * placeholder text to be shown when no items are selected
     */
    @Input()
    placeholder: string;

    /*
     * Label for add new item button
     * */
    @Input()
    createLabel: string;
    /**
     * Icon for the createLabel
     */
    createLabelIcon = input<string>('plus');
    /*
     * title for add new item dialog
     * */
    @Input()
    createTitle: string;

    @Input()
    dropdownPosition: DropdownPosition = 'auto';

    /**
     * Custom item handler function to be called when the add item button is clicked
     * Overrides the default dialog input
     * */
    @Input()
    addCustomItemHandler?: () => void | Promise<void>;

    /**
     * Manual override control for valid state of input field
     */
    @Input()
    valid = true;

    /**
     * Controlling the z-index for the dropdown panel container with a css variable
     */
    layer = input('--layer-dropdown');

    /**
     * event fired when a new item is selected from the dropdown
     * @param { T } - selected model. if bindLabel is used the current selected label will be returned.
     *
     */
    @Output()
    selectionChange: EventEmitter<unknown | DropdownOption> = new EventEmitter();

    @Output()
    optionAdd: EventEmitter<unknown | DropdownOption> = new EventEmitter();

    /**
     * event fired when the dropdown is opened
     *
     */
    @Output()
    opened: EventEmitter<Event> = new EventEmitter();
    /**
     * event fired when the dropdown is closed
     *
     */
    @Output()
    closed: EventEmitter<DropdownOption> = new EventEmitter();

    @HostBinding('attr.disabled') get disabledState() {
        return this.disabled ? 'disabled' : null;
    }
    @HostBinding('attr.class') get hostClassNames() {
        return ['ur-dropdown', this.labelOutside ? 'ur-dropdown-label-outside' : ''].filter(Boolean).join(' ');
    }
    @HostBinding('attr.aria-labelledby') get hostId() {
        return this.uuid;
    }

    @ViewChild('addItemButton')
    addItemButton?: HTMLButtonElement;

    @ViewChild('container')
    container?: ElementRef;

    @ViewChild('ngselect')
    NgSelect?: NgSelectComponent;

    constructor(
        private config: NgSelectConfig,
        private readonly translate: TranslateService,
        private readonly dialogService: URPortalService,
        private readonly elementRef: ElementRef,
    ) {
        this.config.notFoundText = this.translate.instant('ui.dropdown.not_found_text');
        this.placeholder = this.translate.instant('ui.dropdown.placeholder');
        this.createLabel = this.translate.instant('ui.dropdown.create_label');
        this.createTitle = this.translate.instant('ui.dropdown.create_title');
        this.panelContainer = document.querySelector('.ur-dropdown-panel-container');
        if (!this.panelContainer) {
            this.panelContainer = document.createElement('div');
            this.panelContainer.classList.add('ur-dropdown-panel-container');
            const container = document.querySelector('body');
            container?.appendChild(this.panelContainer);
        }
        // Re-position dropdown panel on global scroll event
        this.subscriptions.add(
            fromEvent(window, 'scroll', { capture: true })
                .pipe(
                    debounceTime(50),
                    distinctUntilChanged(),
                    filter(() => !!this.NgSelect?.isOpen),
                )
                .subscribe((event) => {
                    const isScrollingInScrollHost = (event.target as HTMLElement).classList?.contains('ng-dropdown-panel-items');
                    // If panel is rendered on top of dropdown or
                    // if the scroll event is coming from the dropdown panel, ignore it
                    if (this.NgSelect?.dropdownPanel?.currentPosition === 'top' || isScrollingInScrollHost) {
                        return;
                    }
                    this.rePositionDropdownPanel(event);
                }),
        );
        // Close the dropdown on pointerdown outside of the dropdown
        this.subscriptions.add(
            fromEvent(document, 'pointerdown', { capture: true })
                .pipe(filter(() => !!this.NgSelect?.isOpen))
                .subscribe(($event) => {
                    const path = $event.composedPath && $event.composedPath();
                    const panel = this.NgSelect?.dropdownPanel.scrollElementRef.nativeElement.parentElement;
                    if (!path.includes(panel) && !path.includes(this.elementRef?.nativeElement) && this.NgSelect?.isOpen) {
                        this.NgSelect?.close();
                    }
                }),
        );
        const allCloseEvents$ = merge(
            fromEvent(window, 'ur-error', { capture: true }), // Close the dropdown on error
            fromEvent(window, DIALOG_KEYS.OPEN_EVENT, { capture: true }), // Close the dropdown on dialog open event
        );
        this.subscriptions.add(allCloseEvents$.subscribe(() => this.closeNgSelect()));
    }

    closeNgSelect() {
        if (this.NgSelect?.isOpen) {
            this.NgSelect?.close();
        }
    }

    onOpen() {
        // in lack of better alternatives this is how to prevent
        // the dropdown to open if disabled
        if (this.disabled) {
            this.NgSelect?.close();
            return;
        }
        // Because it is appended to body element and
        // There are no event for handling afterOpened it's wrapped in a animationframe to get next tick
        requestAnimationFrame(() => {
            if (this.NgSelect?.dropdownPanel?.currentPosition !== 'top') {
                this.rePositionDropdownPanel();
            }
        });

        // because the dropdown-panel can be rendered to the body
        const numbOfItems = this.NgSelect?.itemsList?.items?.length || 0;
        const numOfOptions = numbOfItems <= this.numberOfOptionsVisible ? numbOfItems : this.numberOfOptionsVisible;
        this.panelContainer?.style.setProperty('--dropdown-options-visible', numOfOptions.toString());
        this.panelContainer?.style.setProperty('--dropdown-layer', `var(${this.layer()})`);
        this.isOpen = true;
        this.opened.emit();
    }

    rePositionDropdownPanel(event?: Event) {
        const element = this.NgSelect?.dropdownPanel?.scrollElementRef.nativeElement;
        if (!element) {
            return;
        }
        const panelRect = element.getBoundingClientRect();
        const dropdownRect = this.container?.nativeElement.getBoundingClientRect();

        // EP-11528: if the dropdown panel is outside the viewport, move it to the left
        if (window.innerWidth < dropdownRect.left + panelRect.width) {
            element.parentElement.style.left = `inherit`;
            element.parentElement.style.right = `${window.innerWidth - dropdownRect.right}px`;
        }
        // if the reposition comes from a scroll evnet, move the panel to the bottom of the dropdown
        if (event) {
            const posY = dropdownRect.top + dropdownRect.height;
            element.parentElement.style.top = `${(window.innerHeight - posY) * -1}px`;
        }
    }

    onClose() {
        this.isOpen = false;
        this.closed.emit(this.NgSelect?.itemsList?.selectedItems[0]?.value);
    }
    onFocus(event: Event) {
        // mimic keyboard focus. If $event doesn't have sourceCapabilities it's most likely a keyboard event
        this.isFocusVisible = !!(event as any)?.['sourceCapabilities'];
    }
    onBlur() {
        this.isFocusVisible = false;
    }
    getLabel(item: string | { [key: string]: any }) {
        if (typeof this.labelFactory === 'function') {
            return this.labelFactory(item);
        }
        const key = this.bindLabel || 'label';
        if (typeof item === 'string') {
            return item;
        }
        return item[key];
    }

    async onItemAddClick(event: Event) {
        event.preventDefault();
        event.stopPropagation();
        if (this.disabled) {
            return;
        }

        this.NgSelect?.close();

        if (typeof this.addCustomItemHandler === 'function') {
            return await this.addCustomItemHandler();
        }

        const dialogRef = this.dialogService.open<DialogInputReturnData, DialogInputData>(URDialogInputContainerComponent, {
            validators: this.validators || [],
            value: this.createValue,
            title: this.createTitle,
            label: this.createLabel,
            placeholder: this.placeholder,
            type: 'text',
        });
        const { reason, returnData } = await firstValueFrom(dialogRef.afterClose);

        if (reason === CloseReason.CONFIRMED && returnData?.value) {
            this.optionAdd.emit(returnData.value);
        }
    }

    public getSelectedOption() {
        return this.NgSelect?.itemsList?.selectedItems[0] ?? {};
    }
    public getPlaceholderLabel() {
        const selectedItem = this.NgSelect?.itemsList?.selectedItems[0];
        if (!selectedItem) {
            return this.placeholder;
        }
        return typeof this.labelFactory === 'function' ? this.labelFactory(selectedItem.value) : selectedItem.label;
    }

    clear() {
        this.NgSelect?.handleClearClick();
        this.selectedOption = undefined;
    }
    public ngOnDestroy(): void {
        this.NgSelect?.close();
        this.subscriptions.unsubscribe();
    }
}
