import { NgClass, UpperCasePipe } from '@angular/common';
import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewChild } from '@angular/core';
import { URSignalFooterComponent } from '../SignalFooter/ur-signal-footer.component';

@Component({
    selector: 'ur-analog-signal-display',
    templateUrl: './ur-analog-signal-display.component.html',
    styleUrls: ['./ur-analog-signal-display.component.scss'],
    imports: [NgClass, URSignalFooterComponent, UpperCasePipe],
})
export class URAnalogSignalDisplayComponent implements AfterViewInit, OnChanges {
    @ViewChild('slider') slider?: ElementRef;

    /**
     * The Signal ID
     */
    @Input()
    signalID?: string;

    /**
     *  Whether the signal is an input
     */
    @Input()
    isInput?: boolean;

    /**
     * Whether to display as power supply
     */
    @Input()
    isPower?: boolean;

    /**
     * The domain of the Analog Signal
     */
    @Input()
    domain?: string;

    /**
     * The current value of the Signal
     */
    @Input()
    value!: number;

    /**
     * The minimum value for the value
     * Used for showing slider range
     * and limiting input
     * Precondition: Must be greater than or equal to 0
     */
    @Input()
    min = 0;

    /**
     * The maximum value for the value
     * Used for showing slider range
     * and limiting input
     */
    @Input()
    max = 0;

    /**
     * The number of digits to display
     */
    @Input()
    significantDigits?: number;

    /**
     * The unit of the value
     */
    @Input()
    unit?: string;

    /**
     * Footer label
     */
    @Input()
    label?: string;

    /**
     * Whether the signal has an assigned action preset
     */
    @Input()
    hasActionPreset = false;

    /**
     * Event emitted whenever the slider is moved
     */
    @Output()
    valueChanged = new EventEmitter<number>();

    ngAfterViewInit() {
        this.updateSliderFill(this.value);
    }

    ngOnChanges() {
        this.updateSliderFill(this.value);
    }

    handleValueChange(event: Event) {
        const target = event.target as HTMLInputElement;
        this.value = parseFloat(target.value);
        this.valueChanged.emit(this.value);
        this.updateSliderFill(this.value);
    }

    formatNumber(value: number): string {
        return value.toLocaleString('en-US', {
            minimumFractionDigits: this.significantDigits,
            maximumFractionDigits: this.significantDigits,
        });
    }

    updateSliderFill(value: number) {
        if (this.slider) {
            const percentage = ((value - this.min) / (this.max - this.min)) * 100;
            this.slider.nativeElement.style.setProperty('--webkitProgressPercent', percentage + '%');
        }
    }
}
