import { ChangeDetectionStrategy, Component, computed, EventEmitter, input, Output } from '@angular/core';
import { URIconButtonComponent } from '../IconButton/ur-icon-button.component';

export const states = ['playing', 'paused', 'stopped', 'stepping', 'off'] as const;
export type State = (typeof states)[number];

type ControlStates = 'play' | 'pause' | 'stop' | 'step';

@Component({
    selector: 'ur-playback',
    templateUrl: './ur-playback.component.html',
    styleUrls: ['./ur-playback.component.scss'],
    imports: [URIconButtonComponent],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class URPlaybackComponent {
    /**
     * State of the playback control
     */
    state = input<State>('off');

    /**
     * Whether to allow stepping
     */
    allowStepping = input<boolean>(true);

    /**
     * Whether the control is disabled or not
     */
    disabled = input<boolean>(false);

    /**
     * Will show stop button even if the state is not playing or stepping
     */
    alwaysAllowStop = input<boolean>(false);

    /**
     * emits the state change if the user interacts with one of the controls
     */
    @Output()
    withStateChange: EventEmitter<ControlStates> = new EventEmitter();

    // Play button is shown when not playing or stepping
    showPlayBtn = computed(() => this.state() !== 'playing' && this.state() !== 'stepping');
    disablePlayBtn = computed(() => this.state() === 'off' || this.disabled() || this.alwaysAllowStop());
    disableStepping = computed(() => {
        const isSteppingAllowed = this.allowStepping();
        const isOff = this.state() === 'off';
        const isPlaying = this.state() === 'playing';
        const isStepping = this.state() === 'stepping';
        const isDisabled = this.disabled();
        const showStop = this.alwaysAllowStop();

        return !isSteppingAllowed || isOff || isPlaying || isStepping || isDisabled || showStop;
    });

    public onChange(state: ControlStates) {
        if (this.state() !== 'off') {
            this.withStateChange.emit(state);
        }
    }
}
