import { NgClass } from '@angular/common';
import {
    ChangeDetectionStrategy,
    Component,
    computed,
    ElementRef,
    EventEmitter,
    HostListener,
    input,
    Output,
    ViewChild,
    ViewEncapsulation,
} from '@angular/core';
import { ButtonType, ButtonSize } from '../../models';
import { URIconComponent } from '../Icon/ur-icon.component';

/** Main Button component used where a call to actions is required. */
@Component({
    selector: 'ur-button',
    templateUrl: './ur-button.component.html',
    styleUrls: ['./ur-button.component.scss'],
    encapsulation: ViewEncapsulation.None,
    imports: [NgClass, URIconComponent],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class URButtonComponent {
    @ViewChild('urButtonElement') buttonElement: ElementRef | undefined;

    warning = input<boolean>(false);
    /**
     * Whether to highlight the button as selected/active
     */
    active = input<boolean>(false);

    /**
     * Name of the icon to use
     */
    icon = input<string>('');

    /**
     * Alignment of icon relative to label
     */
    iconAlign = input<'left' | 'right'>('left');

    /**
     * Determine the style of the button by type
     */
    type = input<ButtonType>('cta');

    /**
     * How large should the button be?
     * @deprecated size will be removed in the future, so please dont use the Size property
     */
    size = input<ButtonSize>('large');

    /**
     * Use to disable the button
     */
    disabled = input<boolean>(false);

    iconSize = computed(() => {
        return this.type() === 'icon' && this.size() === 'large' ? 'medium' : 'small';
    });

    /**
     * click event listener
     */
    @Output()
    clicked: EventEmitter<Event> = new EventEmitter();

    /**
     * Click event listener
     */
    @Output()
    handleClick: EventEmitter<Event> = new EventEmitter();

    @HostListener('click', ['$event'])
    public hostClick(event: MouseEvent): void {
        // Because there can be event-listeners on the host/web-component
        // we need to catch the bobbling click from the child button and stop
        // propagation at this level.
        event.preventDefault();
        event.stopPropagation();
    }

    onClick($event: Event) {
        $event.preventDefault();
        // clicked will be renamed in the future, so please use handleClick()
        this.clicked.emit($event);
        this.handleClick.emit($event);
    }

    triggerFocus() {
        this.buttonElement?.nativeElement.focus();
    }
}
