import { ComponentType } from '@angular/cdk/overlay';
import { Component, ComponentRef, HostBinding, ViewChild, ViewContainerRef, Output, EventEmitter } from '@angular/core';
import { URModalService } from '../../../services/ur-modal.service';

@Component({
    selector: 'ur-modal-renderer',
    templateUrl: './ur-modal-renderer.component.html',
    styleUrls: ['./ur-modal-renderer.component.scss'],
    standalone: true,
})
export class URModalRendererComponent {
    @ViewChild('content', { read: ViewContainerRef })
    content?: ViewContainerRef;

    private componentInstance: ComponentRef<any> | null = null;

    /**
     * opened event listener
     */
    @Output()
    opened: EventEmitter<Event> = new EventEmitter();

    /**
     * closed event listener
     */
    @Output()
    closed: EventEmitter<Event> = new EventEmitter();

    constructor(private readonly modalService: URModalService) {
        modalService.registerRenderer(this);
    }

    @HostBinding('style.display') get display() {
        return this.componentInstance?.instance ? 'flex' : 'none';
    }

    renderModal(component: ComponentType<any>, data?: { [key: string]: any }) {
        if (component && this.content) {
            this.componentInstance = this.content.createComponent(component);

            for (const key in data) {
                if (Object.prototype.hasOwnProperty.call(data, key)) {
                    this.componentInstance.instance[key] = data[key];
                }
            }

            this.opened.emit();
        }
    }

    removeModal() {
        if (this.componentInstance) {
            this.componentInstance.instance.visible = true;
            this.componentInstance.destroy();
            this.componentInstance = null;
            this.closed.emit();
        }
    }
}
