import { Component, HostBinding, Input, ViewEncapsulation } from '@angular/core';

@Component({
    selector: 'ur-label',
    templateUrl: './ur-label.component.html',
    styleUrls: ['./ur-label.component.scss'],
    encapsulation: ViewEncapsulation.None,
    standalone: true,
})
export class URLabelComponent {
    /**
     * Label text
     */
    @Input()
    label = 'Label';

    /**
     * show asterisk after the label if used in as form labels
     */
    @Input()
    required = false;

    @Input()
    size?: 'small' | 'medium' | 'large';

    @HostBinding('class')
    get elementClass() {
        let classes = '';
        if (this.required) {
            classes = `${classes} is-required`;
        }
        if (this.size) {
            classes = `${classes} ${this.size.toLowerCase()}`;
        }
        return classes;
    }
}
