import { NgClass } from '@angular/common';
import { Component, Input, OnInit, SimpleChanges, OnChanges } from '@angular/core';

@Component({
    selector: 'ur-skeleton',
    templateUrl: './ur-skeleton.component.html',
    styleUrls: ['./ur-skeleton.component.scss'],
    imports: [NgClass],
})
export class URSkeletonComponent implements OnInit, OnChanges {
    /** loading text for assessive tools */
    @Input()
    ariaValuetext? = 'loading';

    /** enable / disable the animation */
    @Input()
    animation = true;

    /** type: '' | square | circle | line. defaults to 'line' */
    @Input()
    type: 'line' | 'square' | 'circle' = 'line';

    /** number of skeleton items */
    @Input()
    count = 1;

    items: Array<number>;

    constructor() {
        this.items = [];
    }

    ngOnInit(): void {
        this.items = this.generateItems(this.count);
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { count } = changes;
        if (count) {
            this.items = this.generateItems(this.count);
        }
    }
    /** generate a random number between .4 and 1 and return an array with [count] number of items */
    generateItems(count: number): number[] {
        return Array(count || 1)
            .fill(null)
            .map(() => Math.random() * (1 - 0.4) + 0.4);
    }
}
