import { Component, Inject, ViewChild, WritableSignal, signal } from '@angular/core';
import { CloseReason, CloseReasonType, DialogInputData } from '@universal-robots/ui-models';
import { DATA_TOKEN, DialogRef } from '../../../ref';
import { URGenericDialogComponent } from '../../Dialogs/GenericDialog/ur-generic-dialog.component';
import { URInputComponent } from '../Input/ur-input.component';
import { URNumberInputComponent } from '../NumericField/ur-number-input.component';

@Component({
    selector: 'ur-dialog-input-container',
    templateUrl: './ur-dialog-input-container.component.html',
    styleUrls: ['./ur-dialog-input-container.component.scss'],
    imports: [URGenericDialogComponent, URInputComponent, URNumberInputComponent],
})
export class URDialogInputContainerComponent {
    public value: WritableSignal<string> = signal('');
    public isValid: WritableSignal<boolean> = signal(false);
    @ViewChild('dialog') dialog?: URGenericDialogComponent;
    @ViewChild('textInput') textInput?: URInputComponent;
    @ViewChild('numberInput') numberInput?: URInputComponent;

    onBeforeClose?: (value: string | null) => string | null | Promise<string | null>;
    isCheckingBeforeClose = false;

    constructor(
        @Inject(DATA_TOKEN) public data: DialogInputData,
        private readonly dialogRef: DialogRef<unknown>,
    ) {
        this.data = Object.assign(
            {
                validation: {},
                translations: {},
                validators: [],
            },
            this.data,
        );
        this.value.set(String(this.data.value));
    }

    onEnterClick(event: KeyboardEvent) {
        event.stopPropagation();
        event.stopImmediatePropagation();
        // On Enter close dialog with current value saved
        this.onDialogClose({ reason: CloseReason.CONFIRMED });
    }

    async onDialogClose(event: { reason: CloseReasonType }) {
        let message;
        if (typeof this.data.onBeforeClose === 'function') {
            this.isCheckingBeforeClose = true;
            message = await this.data.onBeforeClose.call(this, this.value(), event.reason);
            this.isCheckingBeforeClose = false;
        }
        // if onBeforeClose is returning a string it's interpreted as an error
        if (message) {
            const input = this.data.type === 'number' ? this.numberInput : this.textInput;
            this.isValid.set(false);
            input?.setMessage({ message, valid: this.isValid() });
        }
        if (this.isValid()) {
            this.dialogRef.close({
                reason: event.reason,
                returnData: {
                    valid: true,
                    value: this.value(),
                },
            });
        }
    }
    onValueChange(value: string | number) {
        this.value.set(String(value));
    }

    onValidChange(valid: boolean) {
        this.isValid.set(valid);
    }
}
