import { ChangeDetectionStrategy, Component, computed, inject, input, output, viewChild } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { DropdownOption, TabInputInsertableModel } from '@universal-robots/ui-models';
import { URDropdownComponent } from '../../../Dropdown/ur-dropdown.component';
import { URHeadingComponent } from '../../../Heading/ur-heading.component';

@Component({
    selector: 'ur-quick-access',
    templateUrl: './ur-quick-access.component.html',
    styleUrls: ['./ur-quick-access.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [TranslateModule, URDropdownComponent, URHeadingComponent],
})
export class URQuickAccessComponent {
    translate = inject(TranslateService);

    insertable = input<TabInputInsertableModel | undefined>();
    inputDropdown = viewChild<URDropdownComponent>('inputDropdown');
    outputDropdown = viewChild<URDropdownComponent>('outputDropdown');
    variableDropdown = viewChild<URDropdownComponent>('variableDropdown');
    functionDropdown = viewChild<URDropdownComponent>('functionDropdown');
    builtInDropdown = viewChild<URDropdownComponent>('builtInDropdown');

    inputNameList = computed<DropdownOption[]>(() =>
        this.sortByLabel(
            this.insertable()?.inputs.map((f) => ({
                label: f.label,
                value: f.value,
                details: !f.details ? this.translate.instant('ui.quick_access.no_module_function') : f.details,
            })) ?? [],
        ),
    );

    outputNameList = computed<DropdownOption[]>(() =>
        this.sortByLabel(
            this.insertable()?.outputs.map((f) => ({
                label: f.label,
                value: f.value,
                details: !f.details ? this.translate.instant('ui.quick_access.no_module_function') : f.details,
            })) ?? [],
        ),
    );

    variableNameList = computed<DropdownOption[]>(() =>
        this.sortByLabel(
            this.insertable()
                ?.variables.filter((variable) => !variable.suppressed)
                .map((variable) => ({
                    label: variable.name,
                    value: variable.name,
                })) ?? [],
        ),
    );

    functionsNameList = computed<DropdownOption[]>(() =>
        this.sortByLabel(
            this.insertable()?.functions.map((f) => ({
                label: f.label,
                value: f.value,
                details: !f.details ? this.translate.instant('ui.quick_access.no_module_function') : f.details,
            })) ?? [],
        ),
    );

    builtInFunctionsList = computed<DropdownOption[]>(() =>
        this.sortByLabel(
            this.insertable()?.builtins.map((f) => ({
                label: f.label,
                value: f.value,
            })) ?? [],
        ),
    );

    private sortByLabel<T extends { label: string }>(list: T[]): T[] {
        return [...list].sort((a, b) => a.label.localeCompare(b.label));
    }

    quickAccessSelected = output<string>();
    variableSelected = output<string>();
    functionSelected = output<string>();
    builtInSelected = output<string>();

    isKeyboardVisible(): boolean {
        const el = document.querySelector('.keyboard-main-container');
        return !!el && el.classList.contains('visible');
    }

    onInputClick(inputName: string) {
        if (inputName !== '') {
            this.quickAccessSelected.emit(inputName);
            this.inputDropdown()?.clear();
        }
    }

    onOutputClick(outputName: string) {
        if (outputName !== '') {
            this.quickAccessSelected.emit(outputName);
            this.outputDropdown()?.clear();
        }
    }

    onVariableClick(variableName: string) {
        if (variableName !== '') {
            this.variableSelected.emit(variableName);
            this.variableDropdown()?.clear();
        }
    }

    onFunctionClick(functionName: string) {
        if (functionName !== '') {
            this.functionSelected.emit(functionName + '()');
            this.functionDropdown()?.clear();
        }
    }

    onBuiltInClick(builtInFuncName: string) {
        if (builtInFuncName !== '') {
            this.builtInSelected.emit(builtInFuncName);
            this.builtInDropdown()?.clear();
        }
    }
}
