import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { URDigitalSignalValueDisplayComponent } from '../DigitalSignalValueDisplay/ur-digital-signal-value-display.component';
import { URSignalFooterComponent } from '../SignalFooter/ur-signal-footer.component';

@Component({
    selector: 'ur-digital-signal-display',
    templateUrl: './ur-digital-signal-display.component.html',
    styleUrls: ['./ur-digital-signal-display.component.scss'],
    imports: [NgClass, URDigitalSignalValueDisplayComponent, URSignalFooterComponent],
})
export class URDigitalSignalDisplayComponent {
    /**
     * The Signal ID
     */
    @Input()
    signalID?: string;

    /**
     * Whether the signal is an input
     */
    @Input()
    isInput?: boolean;

    /**
     * The current value of the signal
     */
    @Input()
    value?: boolean;

    /**
     * Whether the signal is configurable
     */
    @Input()
    isConfigurable = false;

    /**
     * Footer label
     */
    @Input()
    label?: string;

    /**
     * Whether the signal has an assigned action preset
     */
    @Input()
    hasActionPreset = false;

    /**
     * Whether the signal has an assigned safety action
     */
    @Input()
    hasSafetyAction = false;

    /**
     * Event emitted when the toggle button is clicked
     */
    @Output()
    clicked = new EventEmitter();
}
