import { ComponentType } from '@angular/cdk/overlay';
import { Injectable } from '@angular/core';

type data = { [key: string]: any };
type callback = (dt: data) => void;
type onClose = (cb: callback) => void;
type renderer = {
    renderModal(c: ComponentType<any>, dt: data): void;
    removeModal(): void;
};
/**
 * @deprecated - use URPortalService instead
 */
@Injectable({
    providedIn: 'root',
})
export class URModalService {
    private modalRenderer?: renderer;
    private currentModal?: ComponentType<any>;
    private closeListeners: callback[] = [];

    openModal(componentClass: ComponentType<any>, data: data = {}): { onClose: onClose } {
        this.currentModal = componentClass;
        this.modalRenderer?.renderModal(componentClass, {
            ...data,
            visible: true,
        });

        return Object.freeze({
            onClose: (cb) => {
                this.closeListeners.push(cb);
            },
        });
    }

    closeModal(data: data = {}) {
        this.closeListeners.forEach((listener) => listener(data));
        this.closeListeners = [];
        this.modalRenderer?.removeModal();
    }

    registerRenderer(renderer: renderer) {
        this.modalRenderer = renderer;
    }
}
