import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    computed,
    ContentChildren,
    effect,
    ElementRef,
    EventEmitter,
    input,
    OnDestroy,
    Output,
    QueryList,
    signal,
    ViewChild,
    ViewEncapsulation,
} from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { debounce, fromEvent, interval, Subscription } from 'rxjs';
import { CloseReason, CloseReasonType } from '@universal-robots/ui-models';
import { URButtonComponent } from '../Button/ur-button.component';
import { URCarouselItemComponent } from '../CarouselItem/ur-carousel-item.component';
import { URRadioButtonComponent, URRadioGroupDirective } from '../Radio/ur-radio-button.component';

interface IntersectionSet {
    isIntersecting: boolean;
    target: any;
}

@Component({
    selector: 'ur-carousel',
    templateUrl: 'ur-carousel.component.html',
    styleUrls: ['ur-carousel.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URRadioGroupDirective, URRadioButtonComponent, URButtonComponent, TranslateModule],
    standalone: true,
})
export class URCarouselComponent implements AfterViewInit, OnDestroy {
    @ContentChildren(URCarouselItemComponent) carouselItems: QueryList<any> = new QueryList();

    @ViewChild('carousel') carousel?: ElementRef;

    @Output() closeDialog = new EventEmitter<{ reason: CloseReasonType }>();

    isCarousel = input(true);

    currentIdx = signal(0);
    prevDisabled = computed(() => this.currentIdx() === 0 && !this.isCarousel());
    nextDisabled = computed(() => this.currentIdx() === this.carouselItems.length - 1 && !this.isCarousel());
    nextButtonText = computed(() => (this.nextDisabled() ? 'ui.carousel.done' : 'ui.carousel.next'));

    observer?: IntersectionObserver;
    hasIntersected: Set<IntersectionSet> = new Set();

    private debounceSubscription: Subscription | undefined;
    constructor() {
        this.initializeIndexEffect();
    }

    private initializeIndexEffect() {
        effect(() => {
            const carouselItem = this.carouselItems.get(this.currentIdx());

            if (carouselItem) {
                this.goToElement(carouselItem);
            }
        });
    }

    goToElement(element: URCarouselItemComponent) {
        if (this.carousel) {
            const scrollport = this.carousel.nativeElement;
            const delta = Math.abs(scrollport.offsetLeft - element?.elm.nativeElement.offsetLeft);
            scrollport.scrollTo(delta, 0);
        }
    }

    next() {
        const nextIdx = this.currentIdx() + 1;

        if (this.isCarousel() && (nextIdx > this.carouselItems.length || nextIdx === this.carouselItems.length)) {
            this.currentIdx.set(0);
            return;
        }

        this.currentIdx.set(nextIdx);
        if (!this.isCarousel() && nextIdx === this.carouselItems.length) {
            this.closeDialog.emit({ reason: CloseReason.CONFIRMED });
        }
    }

    previous() {
        const prevIdx = this.currentIdx() - 1;

        if (this.isCarousel() && prevIdx < 0) {
            this.currentIdx.set(this.carouselItems.length - 1);
            return;
        }

        this.currentIdx.set(prevIdx);
    }

    setupObservers() {
        this.observer = new IntersectionObserver(
            (observations) => {
                for (const observation of observations) {
                    this.hasIntersected.add({
                        isIntersecting: observation.isIntersecting,
                        target: observation.target,
                    });
                }
            },
            {
                root: this.carousel?.nativeElement,
                threshold: 0.5,
            },
        );
    }

    syncState() {
        for (const observation of this.hasIntersected) {
            if (observation.isIntersecting) {
                this.carouselItems.find((item, id) => {
                    if (observation.target === item.elm.nativeElement) {
                        this.currentIdx.set(id);
                        return true;
                    }
                    return false;
                });
            }
        }

        this.hasIntersected.clear();
    }

    listen() {
        for (const item of this.carouselItems) {
            this.observer?.observe(item.elm.nativeElement);
        }

        const scroll = fromEvent(this.carousel?.nativeElement, 'scroll');

        this.debounceSubscription = scroll.pipe(debounce(() => interval(100))).subscribe(() => {
            this.syncState();
        });
    }

    ngAfterViewInit() {
        this.setupObservers();
        this.listen();
    }

    ngOnDestroy() {
        if (this.debounceSubscription) {
            this.debounceSubscription.unsubscribe();
        }

        for (const item of this.carouselItems) {
            this.observer?.unobserve(item.elm.nativeElement);
        }
    }
}
