import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    HostBinding,
    Input,
    OnChanges,
    OnInit,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation,
    computed,
    input,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Key } from 'ts-key-enum';
import { InputValidator, NumberInputType } from '@universal-robots/ui-models';
import { URAutoFocusDirective } from '../../../directives/focus.directive';
import { URCharacterLimitComponent } from '../../CharacterLimit/ur-character-limit.component';
import { URHelperMessageComponent } from '../../HelperMessage/ur-helper-message.component';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { URLabelComponent } from '../../Label/ur-label.component';
import { URField } from '../ur-field';
import { decrement, increment, preventIfNotAllowed, replaceCommaToDot, selectFieldText } from './utils/ur-number-input.funcs';

type UnaryOptions = {
    type: 'increment' | 'decrement';
    min: number | undefined;
    max: number | undefined;

    event: KeyboardEvent;
};
@Component({
    selector: 'ur-number',
    templateUrl: './ur-number-input.component.html',
    styleUrls: ['./ur-number-input.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URLabelComponent, URAutoFocusDirective, URIconComponent, URHelperMessageComponent, URCharacterLimitComponent],
})
export class URNumberInputComponent extends URField implements OnChanges, OnInit, AfterViewInit {
    isDirty = false;
    savedNegative = false;
    unitWidth = 0;
    override type: NumberInputType = 'number';

    /*
     * The native number input field.
     * */
    @ViewChild('input') numericField?: ElementRef<HTMLInputElement>;
    @ViewChild('valueUnitOverlay') valueUnitOverlay?: ElementRef<HTMLSpanElement>;

    @HostBinding('style')
    get style() {
        return {
            '--field-unit-width': this.unitWidth + 'px',
        };
    }
    /*
     * Determine the step count
     */

    @Input()
    step = 1;

    /*
     * Controls if the input only allows positive numbers
     */
    @Input()
    positiveDigitsOnly = false;

    /**
     * Declaring the floating precision
     *
     */
    precision = input<number>(0);
    precisionValidators = computed(() => {
        const validators: Array<InputValidator> = [];
        if (this.precision() > 0) {
            validators.push((value: string | number) => {
                const decimalPlaces = value.toString().split('.')[1]?.length || 0;
                if (decimalPlaces > this.precision()) {
                    return this.precision() === 1
                        ? this.translate.instant('ui.validation.decimalPlace')
                        : this.translate.instant('ui.validation.decimalPlaces', { precision: this.precision() });
                }
                return null;
            });
        }
        return validators;
    });

    numericValidators = computed(() => {
        const { min, max } = this.validation();
        const validators: Array<InputValidator> = [];
        if (this.type === 'number' && min !== undefined) {
            validators.push((value: string | number) => {
                return Number(value) < min ? this.translations?.rangeUnderflowMessage || 'rangeUnderflow message missing' : null;
            });
        }
        if (this.type === 'number' && max !== undefined) {
            validators.push((value: string | number) => {
                return Number(value) > max ? this.translations?.rangeOverflowMessage || 'rangeOverflow missing' : null;
            });
        }
        return validators;
    });

    private _validators: any;
    @Input()
    //@ts-ignore overriding validators to add precision validator
    override set validators(value: InputValidator[]) {
        this._validators = value;
    }
    override get validators() {
        return [...this._validators, ...this.precisionValidators(), ...this.numericValidators()];
    }
    /**
     * Value of the field
     */
    override _value = '0';

    public override get value() {
        return this._value;
    }

    @Input()
    public override set value(value: string) {
        if (isNaN(Number(value))) {
            this._value = '0';
        } else {
            this._value = value;
        }

        if (this.isDirty) {
            this.checkValidators(this.value);
            this.valueChanged.emit(this.value);
        }
        this.cd.detectChanges();
    }

    constructor(
        protected override readonly translate: TranslateService,
        protected override readonly cd: ChangeDetectorRef,
    ) {
        super(translate, cd);
    }

    get clearable(): boolean {
        return !this.disabled && !this.readOnly && this.valid && !this.showValidationIcon;
    }

    //Will clear value to 0
    override clearValue() {
        this.value = '0';
        this.cd.detectChanges();
    }
    //Lifecycle methods
    ngOnInit() {
        this.setKeyboardLayout();
        if (isNaN(Number(this.value))) {
            this.clearValue();
        }

        this.checkValidators(this.value);
        // emit the valid change on initial because between open and closed
        // if the valid state did not change it will never be emitted
        // timeout to avoid NG0100
        setTimeout(() => this.validChanged.emit(this.valid), 0);

        this.cd.detectChanges();
    }

    ngAfterViewInit() {
        if (this.numericField) {
            selectFieldText(this.numericField);
        }
        this.unitWidth = this.valueUnitOverlay?.nativeElement?.offsetWidth || 0;
        this.cd.detectChanges();
    }

    ngOnChanges(changes: SimpleChanges) {
        const { value } = changes;
        this.value = value?.currentValue || this.value;
        this.setKeyboardLayout();
        this.cd.detectChanges();
    }

    /*
     * Appending the decimal when user focus off
     */
    override onBlur(event: Event) {
        let { value } = event.target as HTMLInputElement;
        value = replaceCommaToDot(value);
        this.value = value.length === 0 ? '0' : value;
        this.cd.detectChanges();
    }

    /**
     * Internal way to detect field value change
     */
    onInput(event: Event) {
        const target = event.target as HTMLInputElement;
        this.isDirty = true;

        this.replaceZeroValue(Number((event as InputEvent).data));

        this.value = target.value.length === 0 ? '' : target.value;
        this.cd.detectChanges();

        //Prevents for inserting multiple 0's
        if (this.value !== '' && Number(this.value) === 0 && !this.value.includes('.')) {
            this.clearValue();
        }
    }
    /*
     * Replaces the value with the inserted value (event.data) if the number is 0
     * */
    replaceZeroValue(data: number) {
        if (this.value && Number(this.value) === 0 && !this.value.includes('.') && data !== 0) {
            this.value = String(data);
        }
    }

    /*
     * Handle the key down event
     * Mimic's step sequence like on the number input field
     */
    onKeyDown(event: KeyboardEvent) {
        const notAllowedPeriodSign = event.key === '.' && this.value.indexOf('.') > 0;
        if (event.isComposing) {
            return;
        }
        this.isDirty = true;

        if (preventIfNotAllowed(event.key) || (this.isSignKey(event.key) && this.positiveDigitsOnly) || notAllowedPeriodSign) {
            event.preventDefault();
            return;
        }

        if (this.isSignKey(event.key) && !this.positiveDigitsOnly) {
            event.preventDefault();
            this.updateValueSign(event.key, (event.currentTarget as HTMLInputElement).value);
        }

        const { max, min } = this.validation();
        const type = event.key === Key.ArrowUp ? 'increment' : event.key === Key.ArrowDown ? 'decrement' : null;
        //Store the current value - this will be used to check next value.
        if (type) {
            event.preventDefault();
            this.value = this.unary({
                type,
                min,
                max,
                event,
            });
        }
        this.cd.detectChanges();
    }

    /**
     * Handle key up event
     * @param event
     * when
     */
    onKeyUp(event: KeyboardEvent) {
        if (event.isComposing) {
            return;
        }

        this.isDirty = true;

        if (this.savedNegative && Number(this.value) > 0) {
            this.value = `-${this.value}`;
            this.savedNegative = false;
        }
        // the situation where the user deletes and there is only the negative sign left
        if (event.key === Key.Backspace && (event.currentTarget as HTMLInputElement).value === '-') {
            this.value = '';
        }
        this.cd.detectChanges();
    }

    /**
     * increment or decrement value
     */
    private unary({ type, min }: UnaryOptions): string {
        const valToNum = Number(this.value);
        const step = this.step ?? 1;
        const nextVal = type === 'increment' ? increment(valToNum, step) : decrement(valToNum, step);
        if (type === 'decrement' && min !== undefined && nextVal <= 0 && this.positiveDigitsOnly) {
            return '0';
        }
        return nextVal.toString();
    }

    /**
     * update value sign
     */
    private updateValueSign(newSign: string, newValue: string) {
        if (Number(newValue) === 0 && newSign === '-') {
            this.savedNegative = true;
            this.cd.detectChanges();
            // no need to negate zero
            return;
        }
        // handle the special scenario when users enter minus sign as the first input after focused
        if (!this.isDirty && newSign === '-' && newValue === '') {
            this.value = '';
            this.savedNegative = true;
            this.cd.detectChanges();
            return;
        }
        const sign = Number(this.value) < 0 ? '-' : '+';
        if (sign !== newSign) {
            this.value = newSign === '-' ? `-${this.value}` : this.value.replace('-', '');
            this.cd.detectChanges();
        }
    }

    /**
     * setting current keyboard layout
     */
    private setKeyboardLayout(): void {
        if (this.precision() === 0 && this.positiveDigitsOnly) {
            this.keyboardLayout = 'numeric-fixed-positive';
        } else if (this.precision() === 0 && !this.positiveDigitsOnly) {
            this.keyboardLayout = 'numeric-fixed';
        } else if (this.precision() > 0 && this.positiveDigitsOnly) {
            this.keyboardLayout = 'numeric-free-positive';
        } else if (this.precision() > 0 && !this.positiveDigitsOnly) {
            this.keyboardLayout = 'numeric-free';
        } else {
            this.keyboardLayout = 'alpha-numeric';
        }
        this.cd.detectChanges();
    }

    public getValue(value?: string) {
        return replaceCommaToDot(value ?? this.value);
    }
    private isSignKey(key: string) {
        return ['-', '+'].includes(key);
    }
}
