import { ChangeDetectionStrategy, Component, output } from '@angular/core';
import { URButtonComponent } from '../../../Button/ur-button.component';
import { URIconComponent } from '../../../Icon/ur-icon.component';
import { URUtilityIconComponent } from './ur-utility-icon.component';

interface URUtilityButtonModel {
    iconName: string;
    expression: string;
}

@Component({
    selector: 'ur-utility-buttons',
    templateUrl: './ur-utility-buttons.component.html',
    styleUrls: ['./ur-utility-buttons.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URButtonComponent, URIconComponent, URUtilityIconComponent],
})
export class URUtilityButtonsComponent {
    public expressionAdded = output<string>();

    protected readonly utilityButtons: URUtilityButtonModel[] = [
        { iconName: 'xor', expression: 'xor' },
        { iconName: 'and', expression: 'and' },
        { iconName: 'or', expression: 'or' },
        { iconName: 'not', expression: 'not' },
        { iconName: 'true', expression: 'True' },
        { iconName: 'false', expression: 'False' },
        { iconName: 'plus', expression: '+' },
        { iconName: 'minus', expression: '-' },
        { iconName: 'divide', expression: '/' },
        { iconName: 'modulo', expression: '%' },
        { iconName: 'equal', expression: '=' },
        { iconName: 'notEqual', expression: '!=' },
    ];

    protected buttonClicked(expression: string) {
        this.expressionAdded.emit(expression);
    }
}
