import { isEqual } from 'lodash';

/**
 * Maintains a reference to the local storage key and offers functionality to add, retrieve, and remove items from the local storage list.
 */
export class LocalStorageList<T> {
    private localStorageKey: string;
    constructor(key: string) {
        this.localStorageKey = key;
    }
    /**
     * Save data in local storage
     * @param item - stringable data to save in local storage
     */
    public add(data: T | Array<T>): void {
        const localStorageItems = JSON.parse(window.localStorage.getItem(this.localStorageKey) || '[]');
        const list = Array.isArray(data) ? data : [data];
        window.localStorage.setItem(this.localStorageKey, JSON.stringify([...localStorageItems, ...list]));
    }
    /**
     * Remove data from local storage
     * @param item
     */
    remove(item: T): void {
        const items = JSON.parse(window.localStorage.getItem(this.localStorageKey) || '[]');
        window.localStorage.setItem(this.localStorageKey, JSON.stringify(items.filter((lsItem: T) => !isEqual(lsItem, item))));
    }
    /**
     * Get latest added item from local storage. Be aware: this function does not mutate local storage
     * @returns reference to latest added item from local storage
     */
    getLatest(): string | undefined {
        const items = JSON.parse(window.localStorage.getItem(this.localStorageKey) || '[]');
        return items.pop();
    }
    /**
     * Get the first item from local storage. Be aware: this function does not mutate local storage
     * @returns reference to the first added item from local storage
     */
    getFirst(): string | undefined {
        const items = JSON.parse(window.localStorage.getItem(this.localStorageKey) || '[]');
        return items.shift();
    }
    /**
     * Clear local storage with the current key
     */
    clear = (): void => {
        window.localStorage.removeItem(this.localStorageKey);
    };
    length(): number {
        return JSON.parse(window.localStorage.getItem(this.localStorageKey) || '[]').length;
    }
}
