import { AnimationEvent } from '@angular/animations';
import {
    Component,
    ContentChild,
    ElementRef,
    EventEmitter,
    HostBinding,
    HostListener,
    Input,
    OnDestroy,
    Output,
    TemplateRef,
    ViewChild,
    signal,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { CloseReason, DialogCloseEvent, DialogSize, DialogSizeType } from '@universal-robots/ui-models';
import { ANIMATION_STATE, ANIMATION_TRIGGER } from '../../animations';
import { DialogRef } from '../../ref';

@Component({
    selector: 'ur-dialog-master',
    template: '',
    standalone: true,
})
export class URDialog implements OnDestroy {
    readonly ANIMATION_STATE = ANIMATION_STATE;
    /**
     * show dialog header with title and close-icon
     */
    @Input() showHeader = true;

    /**
     * Icon in the title of the dialog
     */
    @Input() icon?: string;

    /**
     * Dialog size
     */
    @Input() size: DialogSizeType = DialogSize.DEFAULT;

    /**
     * Title on the dialog
     */
    @Input() title?: string;

    /**
     * Whether the user is allowed to close the dialog
     * @deprecated set disableClose in DialogConfig instead
     * @example
     * ```typescript
     *  this.portalService.open(URDialogComponent, {
     *      disableClose: true
     *  });
     * ```
     */

    @Input() disableClose = false;

    @Input() raiseForKeyboard = true;

    /**
     * Whether the dialog can be auto-closed by the system.
     * If true, the dialog can only be closed by the user.
     */
    @Input() critical = false;

    /**
     * When the user has clicked the confirm button
     */
    @Output() beforeClose = new EventEmitter<DialogCloseEvent>();

    passiveState = signal<string>(ANIMATION_STATE.ACTIVE);
    visibleState = signal<string>(ANIMATION_STATE.HIDDEN);
    enterDone = signal<boolean>(false);
    $subscriptions = new Subscription();
    // bindings
    @ViewChild('dialogWrapper') dialogWrapper?: ElementRef<HTMLDivElement>;
    @ContentChild('dialogFooter') dialogFooter?: TemplateRef<HTMLElement>;

    @HostBinding('attr.data-size') get sizeAttr() {
        if (this.size) {
            return this.size.replace('_', '-').toLowerCase();
        }
        return DialogSize.DEFAULT.replace('_', '-').toLowerCase();
    }
    @HostBinding('attr.role') get role() {
        return 'dialog';
    }
    @HostBinding('attr.aria-modal') get ariaModal() {
        return 'true';
    }
    @HostBinding('attr.aria-labelledby') get ariaLabelledby() {
        return this.title ? `dialog-label-${this.dialogRef.id}` : null;
    }
    @HostListener('keydown.escape', ['$event']) onKeydownHandler(event: KeyboardEvent) {
        event.stopPropagation();
        // Close only if neither config.disableClose nor this.disableClose is true
        const configAllowsClose = !this.dialogRef.config?.disableClose;
        const componentAllowsClose = !this.disableClose;

        if (configAllowsClose && componentAllowsClose) {
            this.dialogRef.close({ reason: CloseReason.CANCELLED });
        }
    }

    constructor(public dialogRef: DialogRef<unknown>) {
        this.$subscriptions.add(
            this.dialogRef.animationState.subscribe((animationState) => {
                const { animation, state } = animationState;
                if (animation === ANIMATION_TRIGGER.ENTER) {
                    this.visibleState.set(state);
                    this.enterDone.set(true);
                }
                if (animation === ANIMATION_TRIGGER.PASSIVE) {
                    this.passiveState.set(state);
                }
            }),
        );
    }

    onAnimationDone($event: AnimationEvent) {
        this.dialogRef.setAnimationState({ animation: $event.triggerName, state: $event.toState, $event });
    }

    ngOnDestroy() {
        this.$subscriptions.unsubscribe();
    }
}
