import { Component, ViewEncapsulation } from '@angular/core';

/**
 * Used to content projection of a custom icon in a tile button.
 * '@example
 * ```html
 *  <ur-tile-button>
 *     <ur-tile-icon>
 *        <svg>...</svg>
 *     </ur-tile-icon>
 *     label
 * </ur-tile-button>
 * ```
 */
@Component({
    selector: 'ur-tile-icon',
    template: `<ng-content></ng-content>`,
    styleUrls: ['./ur-tile-icon.component.scss'],
    encapsulation: ViewEncapsulation.None,
    standalone: true,
    styles: `
        ur-tile-icon > svg {
            fill: currentColor;
        }
    `,
})
export class URTileIconComponent {}
