import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'ur-digital-signal-value-display',
    templateUrl: './ur-digital-signal-value-display.component.html',
    styleUrls: ['./ur-digital-signal-value-display.component.scss'],
    imports: [NgClass],
})
export class URDigitalSignalValueDisplayComponent {
    /**
     * Whether the value displayed is an output value
     */
    @Input()
    isOutput = false;

    /**
     * Whether the value displayed is from a configurable Signal
     */
    @Input()
    isConfigurable = false;

    /**
     * Whether the value is high or not
     */
    @Input()
    isHigh = false;

    /**
     * Whether the value has a safety action assigned
     */
    @Input()
    hasSafetyAction = false;

    /**
     * The event emitted when the value is clicked
     */
    @Output()
    clicked = new EventEmitter();

    onClick($event: MouseEvent) {
        if (this.isOutput && !this.hasSafetyAction) {
            $event.stopPropagation();
            this.clicked.emit();
        }
    }
}
