// number-input.component.ts
import { CommonModule, KeyValuePipe } from '@angular/common';
import {
    AfterViewInit,
    Component,
    computed,
    ContentChild,
    ElementRef,
    HostBinding,
    inject,
    input,
    model,
    OnDestroy,
    OnInit,
    output,
    signal,
    TemplateRef,
    viewChild,
} from '@angular/core';
import { FormControlStatus, FormsModule, NgModel } from '@angular/forms';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { distinctUntilChanged, Subscription } from 'rxjs';
import { URValidatorFn, ValidationChangeEvent, VirtualKeyboardLayout } from '@universal-robots/ui-models';
import { fadeInDown, fadeInOut } from '../../../animations';
import { URAutoFocusAndSelectDirective, URMaxLengthDirective, URValidatorsDirective, URNaNValidatorDirective } from '../../../directives';
import { URCharacterLimitComponent } from '../../CharacterLimit/ur-character-limit.component';
import { URHelperMessageComponent } from '../../HelperMessage/ur-helper-message.component';
import { URIconComponent } from '../../Icon/ur-icon.component';

type ValueSize = {
    size: number;
    visibleSize: number;
};

@Component({
    selector: 'ur-number-input',
    standalone: true,
    imports: [
        CommonModule,
        FormsModule,
        URAutoFocusAndSelectDirective,
        URIconComponent,
        URHelperMessageComponent,
        URCharacterLimitComponent,
        URValidatorsDirective,
        URMaxLengthDirective,
        URNaNValidatorDirective,
        TranslateModule,
        KeyValuePipe,
    ],
    templateUrl: './ur-number-input.component.html',
    styleUrl: './ur-number-input.component.scss',
    animations: [fadeInDown({ duration: 300 }), fadeInOut()],
})
export class URNumberComponent implements OnInit, OnDestroy, AfterViewInit {
    @ContentChild('requiredErrorMessage') requiredErrorTemplate: TemplateRef<unknown> | undefined;
    @ContentChild('minErrorMessage') minErrorTemplate: TemplateRef<unknown> | undefined;
    @ContentChild('maxErrorMessage') maxErrorTemplate: TemplateRef<unknown> | undefined;
    @ContentChild('patternErrorMessage') patternErrorTemplate: TemplateRef<unknown> | undefined;
    @ContentChild('customErrorMessage') customErrorTemplate: TemplateRef<unknown> | undefined;
    @ContentChild('NaNErrorMessage') NaNErrorTemplate: TemplateRef<unknown> | undefined;

    @HostBinding('style')
    get style() {
        return {
            '--field-unit-width': this.suffixWidth() + 'px',
        };
    }
    public readonly translate = inject(TranslateService);

    // Subscriptions
    private readonly subscriptions$ = new Subscription();

    // View children
    inputControl = viewChild(NgModel);
    inputElement = viewChild<ElementRef<HTMLInputElement>>('input');

    // Input signals

    /**
     * Controls whether the input field receives focus automatically when it's rendered.
     * Defaults to `true`.
     */
    autoFocus = input(true);

    /**
     * Determines if a clear button is displayed within the input field, allowing users to quickly remove the current value.
     * Defaults to `false`.
     */
    clearable = input(false);

    /**
     * When set to `true`, visually indicates a warning state on the input field, often used for non-critical validation issues or contextual hints.
     * Defaults to `false`.
     */
    warning = input(false);

    /**
     * If `true`, the input field becomes disabled, preventing user interaction (e.g., typing or clicking).
     * Defaults to `false`.
     */
    disabled = input(false);

    /**
     * Specifies the layout of the virtual keyboard
     * to be displayed when the input field gains focus on devices with virtual keyboards.
     * The type `VirtualKeyboardLayout` defines the available keyboard configurations.
     * The available values are:
     * 'numeric-fixed' | 'numeric-fixed-positive' | 'numeric-free' | 'numeric-free-positive' | 'alpha-numeric'
     */
    keyboardLayout = input<VirtualKeyboardLayout>();

    /**
     * Sets the descriptive label associated with the input field, providing context and guidance to the user.
     */
    label = input();

    /**
     * Provides an additional message or hint related to the input field,
     * often displayed below or alongside it for further clarification or instructions.
     * Defaults to an empty string.
     */
    message = input('');

    /**
     * Defines the maximum number of characters allowed in the input field's value.
     */
    maxLength = input<number>();

    /**
     * Sets the minimum acceptable numerical value for the input field.
     * This constraint is typically used for numeric or range-based inputs.
     */
    min = input<number>();

    /**
     * Sets the maximum acceptable numerical value for the input field.
     * This constraint is typically used for numeric or range-based inputs.
     */
    max = input<number>();

    /**
     * Specifies the placeholder text displayed within the input field when it has no value.
     * This provides a hint to the user about the expected input.
     * Defaults to `Empty string`.
     */
    placeholder = input('');

    /**
     * Defines a regular expression pattern that the input field's value must match for native browser validation.
     * The value must be a valid JavaScript regular expression string.
     */
    pattern = input<string>();

    /**
     * When set to `true`, indicates that the input field must have a value for the form to be considered valid.
     * Defaults to `false`.
     */
    required = input(false);

    /**
     * Controls the visibility of a validation icon (e.g., an error or success icon)
     * that provides visual feedback on the input's validity.
     * Defaults to `true`.
     */
    showValidationIcon = input(true);

    /**
     * For numeric input types, defines the stepping interval for incrementing or decrementing the value
     * (e.g., using spin buttons or keyboard shortcuts).
     * Defaults to `1`.
     */
    step = input(1);

    /**
     * Appends a specified string to the end of the input field's displayed value, often used for units (e.g., 'kg', 'mm').
     * This does not affect the underlying value.
     * Default to an empty string.
     */
    suffix = input('');

    /**
     * Accepts an array of custom validator functions (`URValidator<number>`)
     * that allow for more complex validation logic beyond basic constraints.
     * Each validator function should take the input value and return a validation error message (or `undefined` if valid).
     * Default to an empty array.
     */
    validators = input<Array<URValidatorFn>>([]);

    /**
     * Specifies the type of icon to be displayed.
     * Common values include 'error', 'success', or other custom icon identifiers.
     * Defaults to `'error'`.
     */
    validationIcon = input('error');

    /**
     * Represents the current value of the input field.
     * It's a two-way binding, meaning changes to this model update the input, and user input updates this model.
     * The type `<number>` indicates that this input field primarily deals with numerical values.
     */
    value = model<number>();
    // Output events

    /**
     * An event emitter that triggers when the input field loses focus (i.e., the user moves the focus to another element).
     */
    blurred = output<void>();

    /**
     * An event emitter that triggers when the input field gains focus (i.e., the user clicks or tabs into the input field).
     */
    focused = output<void>();

    /**
     * An event emitter that triggers whenever the validation status of the input field changes.
     * The emitted value is a `ValidationChangeEvent` object,
     * which typically contains information about the new validation state
     * (e.g., whether it's valid or invalid and any associated error messages).
     */
    validationChanged = output<ValidationChangeEvent>();

    // Internal signals

    /**
     * @ignore
     * */
    suffixWidth = signal(0);

    /**
     * @ignore
     * */
    suffixLock = signal(false);

    /**
     * @ignore
     * */
    valueSize = signal<ValueSize>({ size: 0, visibleSize: 0 });

    /**
     * Computed signals
     * @ignore
     * */
    validNumber = computed(() => {
        return typeof this.value() === 'number' && !Number.isNaN(this.value());
    });

    /**
     * @ignore
     * */
    keyboardType = computed(() => {
        const min = this.min();
        const isNumber = min !== undefined && !isNaN(min);
        const layout = this.keyboardLayout();
        if (layout) {
            return layout;
        } else if (isNumber && min >= 0) {
            return 'numeric-free-positive';
        } else if (isNumber && min < 0) {
            return 'numeric-free';
        } else {
            return 'alpha-numeric';
        }
    });

    /**
     * @ignore
     * */
    ngOnInit() {
        // for suffix position
        this.valueSize.set(this.countDigits(this.value()?.toString() || '0'));
    }

    /**
     * @ignore
     * */
    ngAfterViewInit() {
        this.subscriptions$.add(
            this.inputControl()
                ?.control.statusChanges.pipe(distinctUntilChanged())
                .subscribe((status) => {
                    this.emitValidationChanged(status);
                }),
        );
    }

    /**
     * @ignore
     * */
    onInput($event: InputEvent) {
        const input = $event.target as HTMLInputElement;
        this.valueSize.set(this.countDigits(input.value));
        this.suffixLock.set(input.scrollWidth > input.clientWidth + 1);
    }

    /**
     * Public methods for external control
     * */
    public reset() {
        this.value.set(undefined);
        this.inputControl()?.control.reset();
        this.emitValidationChanged('VALID');
        this.inputElement()?.nativeElement.focus();
    }

    /**
     * triggering the validation change event
     * @ignore
     * */
    private emitValidationChanged(status: FormControlStatus) {
        this.validationChanged.emit({
            status,
            errors: this.inputControl()?.errors ?? {},
        });
    }

    /**
     * Count the number of characters in a string primary used for calculating the position of the suffix in
     * @ignore
     */
    private countDigits(num: string): ValueSize {
        const commaMatches = num.match(/[.,]+/g);
        return {
            size: num.length,
            visibleSize: commaMatches?.length ? num.replace(/[.,]+/g, '').length + commaMatches.length * 0.5 : num.length,
        };
    }
    /**
     * @ignore
     * */
    ngOnDestroy() {
        this.subscriptions$.unsubscribe();
    }
}
