import { AnimationEvent } from '@angular/animations';
import { DOCUMENT } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Inject,
    Input,
    OnChanges,
    OnDestroy,
    Output,
    SimpleChanges,
    ViewChild,
    ViewEncapsulation,
} from '@angular/core';
import { Subject } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';
import { uniqueAlphaNumericId } from '../../../utils/uniqueAlphaNumericId';
import { urExpansionAnimations } from '../expansion-animations';

export type URExpansionPanelState = 'expanded' | 'collapsed';

@Component({
    selector: 'ur-expansion-panel',
    templateUrl: './ur-expansion-panel.component.html',
    styleUrls: ['./ur-expansion-panel.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    animations: [urExpansionAnimations.bodyExpansion],
    host: {
        class: 'ur-expansion-panel',
    },
    standalone: true,
})
export class URExpansionPanelComponent implements AfterViewInit, OnChanges, OnDestroy {
    private readonly _document: Document;

    /** An event emitted after the body's expansion animation happens. */
    @Output() readonly afterExpand = new EventEmitter<void>();
    /** An event emitted after the body's collapse animation happens. */
    @Output() readonly afterCollapse = new EventEmitter<void>();
    /** An event emitted whenever expanded property have changed. */
    @Output()
    readonly expandedChange: EventEmitter<boolean> = new EventEmitter<boolean>();
    /** Event emitted every time the panel is closed. */
    @Output() readonly closed: EventEmitter<void> = new EventEmitter<void>();
    /** Event emitted every time the panel is opened. */
    @Output() readonly opened: EventEmitter<void> = new EventEmitter<void>();

    /** Whether the toggle indicator should be hidden. */
    @Input()
    get hideToggle(): boolean {
        return this._hideToggle;
    }
    set hideToggle(value: boolean) {
        this._hideToggle = value;
        this._changeDetectorRef.markForCheck();
    }
    private _hideToggle = false;

    private readonly urExpandedClassName = 'ur-expanded';
    /** Whether the panel is expanded. */
    @Input()
    get expanded(): boolean {
        return this._expanded;
    }
    set expanded(expanded: boolean) {
        // Only emit events and update the internal value if the value changes.
        if (this._expanded !== expanded) {
            this._expanded = expanded;
            this.expandedChange.emit(expanded);

            if (expanded) {
                this.opened.emit();
            } else {
                this._body?.nativeElement.parentElement?.classList.remove(this.urExpandedClassName);
                this.closed.emit();
            }

            // Ensures that the animation will run when the value is set outside of an `@Input`.
            // This includes cases like the open, close and toggle methods.
            this._changeDetectorRef.markForCheck();
        }
    }
    private _expanded = false;

    /** Whether the panel is disabled. */
    @Input()
    get disabled(): boolean {
        return this._disabled;
    }
    set disabled(disabled: boolean) {
        this._disabled = disabled;
    }
    private _disabled = false;

    /** The unique panel id. */
    readonly id = `ur-expansion-panel-${uniqueAlphaNumericId()}`;
    /** The unique header id. */
    _headerId = `ur-expansion-panel-header-${uniqueAlphaNumericId()}`;

    /** Stream that emits for when animations are done. */
    readonly _bodyAnimationDone = new Subject<AnimationEvent>();

    /** Stream that emits for changes in `@Input` properties. */
    readonly _inputChanges = new Subject<SimpleChanges>();

    /** Element containing the panel's user-provided content. */
    @ViewChild('body') _body?: ElementRef<HTMLElement>;

    constructor(
        @Inject(DOCUMENT) _document: any,
        private readonly _changeDetectorRef: ChangeDetectorRef,
    ) {
        this._document = _document;

        this._bodyAnimationDone
            .pipe(
                distinctUntilChanged((x, y) => {
                    return x.fromState === y.fromState && x.toState === y.toState;
                }),
            )
            .subscribe((event: AnimationEvent) => {
                if (event.fromState !== 'void') {
                    if (event.toState === 'expanded') {
                        event.element.parentElement.classList.add(this.urExpandedClassName);
                        this.afterExpand.emit();
                    } else if (event.toState === 'collapsed') {
                        this.afterCollapse.emit();
                    }
                }
            });
    }

    ngAfterViewInit() {
        if (this.expanded) {
            this._body?.nativeElement.parentElement?.classList.add(this.urExpandedClassName);
        }
    }

    ngOnDestroy(): void {
        this._inputChanges.complete();
        this._bodyAnimationDone.complete();
        this.opened.complete();
        this.closed.complete();
    }

    ngOnChanges(changes: SimpleChanges) {
        this._inputChanges.next(changes);
    }

    /** Gets the expanded state string. */
    _getExpandedState(): URExpansionPanelState {
        return this.expanded ? 'expanded' : 'collapsed';
    }

    /** Toggles the expanded state of the accordion item. */
    toggle(): void {
        this.expanded ? this.close() : this.open();
    }

    /** Sets the expanded state of the accordion item to false. */
    close(): void {
        this._body?.nativeElement.parentElement?.classList.remove(this.urExpandedClassName);
        this.expanded = false;
    }

    /** Sets the expanded state of the accordion item to true. */
    open(): void {
        this.expanded = true;
    }

    /** Checks whether the expansion panel's content contains the currently-focused element. */
    _containsFocus(): boolean {
        if (this._body) {
            const focusedElement = this._document.activeElement;
            const bodyElement = this._body.nativeElement;
            return focusedElement === bodyElement || bodyElement.contains(focusedElement);
        }

        return false;
    }
}
