import { AsyncPipe } from '@angular/common';
import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnChanges,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject } from 'rxjs';
import { InputTranslation, InputValidator } from '@universal-robots/ui-models';
import { URAutoFocusDirective } from '../../../directives/focus.directive';
import { URCharacterLimitComponent } from '../../CharacterLimit/ur-character-limit.component';
import { URHelperMessageComponent } from '../../HelperMessage/ur-helper-message.component';
import { URLabelComponent } from '../../Label/ur-label.component';

/**
 * The Textarea component will handle multi-line plain-text free-form text.
 */
@Component({
    selector: 'ur-textarea',
    templateUrl: './ur-textarea.component.html',
    styleUrls: ['./ur-textarea.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URLabelComponent, URAutoFocusDirective, URHelperMessageComponent, URCharacterLimitComponent, AsyncPipe],
})
export class URTextareaComponent implements OnChanges, AfterViewInit {
    /**
     * label to describe the field type
     */
    @Input()
    label = '';

    /**
     * helper text (instructional) displayed below the input area
     */
    @Input()
    message = '';

    /**
     * textarea value
     */
    @Input()
    value = '';

    /**
     * placeholder value for when no value
     */
    @Input()
    placeholder = '';

    /**
     * The number of visible text lines for the control. If it is specified, it must be a positive integer.
     */
    @Input()
    rows = 3;

    /**
     * to make the input readonly
     */
    @Input()
    readonly = false;

    /**
     * to disable the input
     */
    @Input()
    disabled = false;

    /**
     * whether the user must enter a value
     */
    @Input()
    required = false;

    /**
     * maximum number of characters the user can enter
     */
    @Input()
    maxlength = 999;

    /**
     * list of domain specific validators to check input against
     *   A validator is a method that takes in an input string
     *   and returns an error message string if the input is not valid
     *   OR null if the input is valid.
     */
    @Input()
    validators: InputValidator[] = [];

    /**
     * validation error messages
     */
    @Input()
    translations: InputTranslation = {};

    /**
     * focuses on the textarea to ensure the keyboard is displayed
     */
    @Input()
    autoFocus = false;

    /**
     * on textarea value change event listener
     */
    @Output()
    valueChanged: EventEmitter<number | string> = new EventEmitter();

    @ViewChild('textarea') protected elementRef?: ElementRef<HTMLInputElement>;

    public valid$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);

    /**
     * currently displayed message text below textarea
     */
    public currentMessage = '';

    constructor(protected readonly translate: TranslateService) {}

    async ngAfterViewInit() {
        const isValid = await this.checkValidators();
        this.valid$.next(isValid);
    }

    async ngOnChanges(changes: SimpleChanges) {
        const { message, value } = changes;
        if (message?.currentValue) {
            this.currentMessage = message?.currentValue;
        }

        if (value) {
            const isValid = await this.checkValidators();
            this.valid$.next(isValid);
        }
    }

    /**
     * internal way to detect field value change
     */
    public async onInput(value: string) {
        const isValid = await this.checkValidators();
        this.valid$.next(isValid);

        this.valueChanged.emit(value);
    }

    /**
     * Native validation
     */
    private checkValidity(): boolean {
        const elementValidity = this.elementRef?.nativeElement?.validity;

        if (typeof elementValidity?.valid === 'boolean') {
            return elementValidity.valid;
        }

        // Invalid! Determine cause and set validation message:

        // Required.
        if (this.required && elementValidity?.valueMissing) {
            this.currentMessage =
                this.translations?.requiredValidationMessage || this.translate.instant('app.validation.missing_text', { type: 'required' });
        }

        return false;
    }

    /**
     * Domain and native validation
     */
    private async checkValidators(): Promise<boolean> {
        const value = this.elementRef?.nativeElement?.value;
        let valid = true;

        // Domain validations (based on passed validators) has priority over native validity.
        for (const validator of this.validators) {
            const errorMessage = await validator(value || this.value);
            if (typeof errorMessage === 'string') {
                this.currentMessage = errorMessage;
                return false;
            } else if (typeof errorMessage === 'object' && errorMessage?.validationType === 'error') {
                this.currentMessage = errorMessage.message;
                return false;
            }
        }

        // Check for native validity:
        if (valid) {
            valid = this.checkValidity();
        }

        if (valid) {
            this.currentMessage = this.message;
        }

        return valid;
    }

    /**
     * current length of the input string
     */
    public charCount() {
        return this.elementRef?.nativeElement?.value.length ?? 0;
    }
}
