import { NgClass } from '@angular/common';
import { Component, Input } from '@angular/core';
import { URIconComponent } from '../../Icon/ur-icon.component';

@Component({
    selector: 'ur-signal-footer',
    templateUrl: './ur-signal-footer.component.html',
    styleUrls: ['./ur-signal-footer.component.scss'],
    imports: [NgClass, URIconComponent],
})
export class URSignalFooterComponent {
    /**
     * Label text
     */
    @Input()
    label?: string;

    @Input()
    type: 'configurable' | 'digital' | 'analog' = 'configurable';

    @Input()
    hasActionPreset = false;

    get classes(): string[] {
        return [`ur-signal-footer-${this.type}`];
    }
}
