import { NgClass } from '@angular/common';
import { Component, input } from '@angular/core';

export type HelperMessageType = 'error' | 'warn' | 'info' | 'success';
type Align = 'left' | 'right' | 'center';

@Component({
    selector: 'ur-helper-message',
    templateUrl: './ur-helper-message.component.html',
    styleUrls: ['./ur-helper-message.component.scss'],
    imports: [NgClass],
})
export class URHelperMessageComponent {
    /**
     * type of message
     */
    type = input<HelperMessageType>('info');
    /**
     * text horizontal alignment
     */
    align = input<Align>('left');

    /**
     * message text
     */
    message = input('');
}
