// custom-number-validator.directive.ts
import { Directive, inject, input } from '@angular/core';
import { AbstractControl, AsyncValidator, NG_ASYNC_VALIDATORS, ValidationErrors } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Guid } from 'guid-typescript';
import { firstValueFrom, from, isObservable, Observable, of } from 'rxjs';
import { last, map, mergeMap, scan } from 'rxjs/operators';
import { URValidationResponse, URValidatorFn } from '@universal-robots/ui-models';

/**
 * Represents a custom validation error object that extends Angular's `ValidationErrors`.
 * This type allows for additional flexibility by supporting custom error keys translation keys.
 *
 * @typedef {InternalURValidationErrors}
 * @keys {string} - The error key, which is a string representing translationKey of the error.
 * @property {string | boolean | { interpolateParams?: object }} [key: string] - Custom error keys with their associated values.
 * - `string`: A descriptive error message translated by consumer.
 * - `boolean`: A flag indicating the presence of the error.
 * - `{ interpolateParams?: object }`: Optional parameters for message interpolation (e.g., for translations).
 *
 * Example:
 * ```typescript
 * const errors: URValidationErrors = {
 *   'validation.required': true,
 *   'validation.maxLength': { interpolateParams: { maxLength: 10 } },
 *   'validation.customError': "This is a custom error message."
 * };
 * ```
 */
type InternalURValidationErrors = ValidationErrors & {
    [key: string]: string | boolean | { interpolateParams?: object };
};
type InternalURValidationResponse = InternalURValidationErrors | URValidationResponse;
type InternalURValidatorFn =
    | URValidatorFn
    | ((
          control: AbstractControl,
      ) => InternalURValidationResponse | Promise<InternalURValidationResponse> | Observable<InternalURValidationResponse>);
@Directive({
    selector: '[urValidators]',
    standalone: true,
    providers: [
        {
            provide: NG_ASYNC_VALIDATORS,
            useExisting: URValidatorsDirective,
            multi: true,
        },
    ],
})
export class URValidatorsDirective implements AsyncValidator {
    translateService = inject(TranslateService);
    urValidators = input.required<Array<InternalURValidatorFn>>();

    private shouldValidate(value: unknown): boolean {
        return value !== null && this.urValidators().length > 0;
    }

    private runValidators(control: AbstractControl): Observable<InternalURValidationErrors | null> {
        return from(this.urValidators()).pipe(
            mergeMap(async (validator) => {
                const val = validator(control);
                let validationResult: InternalURValidationResponse = null;
                if (isObservable(val)) {
                    validationResult = (await firstValueFrom(val)) as InternalURValidationResponse;
                } else {
                    validationResult = await val;
                }

                if (validationResult === null) {
                    return null;
                }
                if (typeof validationResult === 'string') {
                    return { [Guid.raw()]: validationResult };
                }

                // Handle the case where validationResult is an URValidationErrors object
                return Object.entries(validationResult || {}).reduce((acc, [errorKey, value]) => {
                    if (value === true) {
                        const translation = this.translateService.instant(errorKey, {
                            value: control.value,
                        });
                        acc[errorKey] = translation;
                    } else if (typeof value === 'object' && 'interpolateParams' in value) {
                        const translation = this.translateService.instant(errorKey, value.interpolateParams);
                        acc[errorKey] = translation;
                    } else if (typeof value === 'string') {
                        acc[errorKey] = value;
                    }
                    return acc;
                }, {} as ValidationErrors);
            }),
            scan((acc, curr) => ({ ...acc, ...curr }), {} as ValidationErrors),
            last(), // Emit only the final accumulated result
            map((errors) => (Object.keys(errors).length ? errors : null)), // Map accumulated errors to final result
        );
    }

    validate(control: AbstractControl): Observable<InternalURValidationErrors | null> {
        if (!this.shouldValidate(control.value)) {
            return of(null);
        }
        return this.runValidators(control);
    }
}
