import { NgClass, NgTemplateOutlet } from '@angular/common';
import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, input, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CloseReason, CloseReasonType } from '@universal-robots/ui-models';
import { scaleOnEnterAnimation, scaleOnPassiveAnimation } from '../../../animations';
import { DialogRef } from '../../../ref';
import { URButtonComponent } from '../../Button/ur-button.component';
import { URHeadingComponent } from '../../Heading/ur-heading.component';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { URIconButtonComponent } from '../../IconButton/ur-icon-button.component';
import { URDialog } from '../ur-dialog';
import { URDialogFooterComponent } from '../ur-dialog-footer.component';
import { URDialogHeaderComponent } from '../ur-dialog-header.component';

@Component({
    selector: 'ur-generic-dialog',
    templateUrl: './ur-generic-dialog.component.html',
    styleUrls: ['./ur-generic-dialog.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: [scaleOnEnterAnimation, scaleOnPassiveAnimation],
    imports: [
        NgClass,
        URIconComponent,
        URHeadingComponent,
        URIconButtonComponent,
        NgTemplateOutlet,
        URButtonComponent,
        URDialogFooterComponent,
        URDialogHeaderComponent,
    ],
})
export class URGenericDialogComponent extends URDialog implements AfterViewInit {
    /**
     * Raise the dialog if needed to make space for keyboard
     */
    @Input()
    override raiseForKeyboard = false;

    /**
     * Never raise the dialog, even if there are input fields
     * This is needed because the raise for keyboard logic is broken. Can be removed when raise for keyboard logic is refactored.
     */
    @Input()
    neverRaiseForKeyboard = false;

    /**
     * show dialog footer with confirm and cancel buttons
     */
    @Input() showFooter = true;

    /**
     * whether the user is allowed to close the dialog
     * @deprecated set disableClose in DialogConfig instead
     * @example
     * ```typescript
     *  this.portalService.open(URDialogComponent, {
     *      disableClose: true
     *  });
     * ```
     */
    @Input()
    override disableClose = false;

    /**
     * disables the confirm button -used in a validation context to toggle the button's disable state
     */
    @Input()
    canSave = false;

    noPadding = input(false);

    noOverflow = input(false);

    animationsDisabled = input(true);

    constructor(
        dialogRef: DialogRef<unknown>,
        private readonly changeDetectorRef: ChangeDetectorRef,
        private readonly translate: TranslateService,
        private readonly el: ElementRef,
    ) {
        super(dialogRef);
    }

    /**
     * cancel button label
     */
    @Input()
    cancelText = this.translate.instant('app.cancel_button');

    /**
     * confirm button label
     */
    @Input()
    confirmText = this.translate.instant('app.confirm_button');

    ngAfterViewInit(): void {
        const triggerTypes = ['password', 'text', 'number'];
        const input = this.el.nativeElement?.querySelector('input');
        const urTextarea = document.querySelector('ur-textarea');
        if (triggerTypes.includes(input?.type) || urTextarea) {
            // If there is an input descendent element, raise the dialog by half the keyboard height to make sure there's space for it
            this.raiseForKeyboard = true;
            this.changeDetectorRef.detectChanges();
        }
        this.dialogRef.disableClose = this.disableClose;
    }

    // force close the dialog
    public closeDialog(reason: CloseReasonType = CloseReason.DESTROYED) {
        this.dialogRef.close({ reason });
    }

    /**
     * close dialog on cancel, close-icon and esc
     */
    onCloseDialog(event: KeyboardEvent | Event) {
        if (!this.disableClose) {
            event.stopPropagation();
            event.stopImmediatePropagation();
            this.beforeClose.emit({ $event: event, reason: CloseReason.CANCELLED });
            this.dialogRef.close({ reason: CloseReason.CANCELLED });
        }
    }

    onConfirmClick(event: Event) {
        if (this.canSave) {
            this.beforeClose.emit({ $event: event, reason: CloseReason.CONFIRMED });
        }
    }
}
