import { FocusMonitor } from '@angular/cdk/a11y';
import { NgClass } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Host,
    HostBinding,
    Input,
    OnDestroy,
    OnInit,
    Output,
    ViewEncapsulation,
} from '@angular/core';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { urExpansionAnimations } from '../expansion-animations';
import { URExpansionPanelHeaderComponent } from '../ur-expansion-panel-header/ur-expansion-panel-header.component';
import { URExpansionPanelComponent } from '../ur-expansion-panel/ur-expansion-panel.component';

@Component({
    selector: 'ur-expansion-panel-selectable-header',
    templateUrl: './ur-expansion-panel-selectable-header.component.html',
    styleUrls: ['../ur-expansion-panel-shared.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    animations: [urExpansionAnimations.indicatorRotate],
    host: {
        class: 'ur-expansion-panel-selectable-header ur-focus-indicator',
        role: 'button',
        '[attr.id]': 'panel._headerId',
        '[attr.aria-controls]': '_getPanelId()',
        '[attr.aria-expanded]': '_isExpanded()',
        '[attr.aria-disabled]': 'panel.disabled',
        '[class.ur-expanded]': '_isExpanded()',
    },
    imports: [NgClass, URIconComponent],
})
export class URExpansionPanelSelectableHeaderComponent extends URExpansionPanelHeaderComponent implements OnInit, OnDestroy {
    @HostBinding('class.selected') _selected = false;

    @Output()
    readonly expansionToggled: EventEmitter<PointerEvent> = new EventEmitter<PointerEvent>();

    @Output()
    readonly clicked: EventEmitter<void> = new EventEmitter<void>();

    @Output()
    readonly handleClick = new EventEmitter<void>();

    constructor(
        @Host() public override panel: URExpansionPanelComponent,
        protected readonly element: ElementRef,
        protected readonly focusMonitor: FocusMonitor,
        protected readonly changeDetectorRef: ChangeDetectorRef,
    ) {
        super(panel, element, focusMonitor, changeDetectorRef);
    }

    override ngOnInit(): void {
        super.ngOnInit();
    }

    override ngOnDestroy(): void {
        super.ngOnDestroy();
    }

    @Input()
    get selected(): boolean {
        return this._selected;
    }
    set selected(value: boolean) {
        this._selected = value;
        this._changeDetectorRef.markForCheck();
    }

    /** Toggles the expanded state of the panel. */
    onToggle(event: Event): void {
        if (!this.disabled) {
            event.preventDefault();
            event.stopPropagation();
            this.panel.toggle();
            this.expansionToggled.emit(event as PointerEvent);
        }
    }

    /**
     * Emits a clicked event when the header is clicked
     * @param event
     */
    onClick(event: Event): void {
        event.preventDefault();
        event.stopPropagation();
        this.handleClick.emit();
    }
}
