import { animation, keyframes, AnimationTriggerMetadata, trigger, style, animate, transition } from '@angular/animations';
import { darkTokens as designtokens } from '@universal-robots/designtokens';

/**
 *
 * @param timingIn - timing for enter animation
 * @param timingOut - timing for leave animation
 * @param height - animate height (default: false)
 * @returns - angular animation trigger
 */
export const fadeInOut = (timingIn = 200, timingOut = 200, height = false): AnimationTriggerMetadata =>
    trigger('fadeInOut', [
        transition(':enter', [
            style(height ? { opacity: 0, height: 0 } : { opacity: 0 }),
            animate(
                `${timingIn}ms ${designtokens.transitionTimingInOut}`,
                style(height ? { opacity: 1, height: 'fit-content' } : { opacity: 1 }),
            ),
        ]),
        transition(':leave', [
            animate(`${timingIn}ms ${designtokens.transitionTimingInOut}`, style(height ? { opacity: 0, height: 0 } : { opacity: 0 })),
        ]),
    ]);

/**
 * @param timingIn - timing for enter animation in ms
 * @param height - animate height (default: false)
 * @returns - angular animation trigger
 */
export const fadeIn = (timingIn = 200, height = false): AnimationTriggerMetadata =>
    trigger('fadeIn', [
        transition(':enter', [
            style(height ? { opacity: 0, height: 0 } : { opacity: 0 }),
            animate(
                `${timingIn}ms ${designtokens.transitionTimingInOut}`,
                style(height ? { opacity: 1, height: 'fit-content' } : { opacity: 1 }),
            ),
        ]),
    ]);

export const fadeInDown = ({ duration = 200, delay = 0, translate = '100%' }) =>
    trigger('fadeInDown', [
        transition(':enter', [
            style({ visibility: 'hidden' }),
            animation(
                [
                    animate(
                        '{{duration}}ms {{delay}}ms',
                        keyframes([
                            style({
                                visibility: 'visible',
                                opacity: 0,
                                transform: 'translate3d(0, -{{translate}}, 0)',
                                easing: designtokens.transitionTimingInOut,
                                offset: 0,
                            }),
                            style({ opacity: 1, transform: 'translate3d(0, 0, 0)', easing: 'ease', offset: 1 }),
                        ]),
                    ),
                ],
                {
                    params: {
                        delay,
                        duration,
                        translate,
                    },
                },
            ),
        ]),
    ]);
