import { AfterViewInit, Component, ElementRef, inject, input, model, OnInit, viewChildren } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, ReactiveFormsModule, ValidatorFn, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { debounceTime } from 'rxjs';
import { URHelperMessageComponent } from '../../HelperMessage/ur-helper-message.component';

export function NoWhiteSpaceValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
        if (control.value === null) {
            return null;
        }

        return control.value.trim().length === 0 ? { whitespace: true } : null;
    };
}

@Component({
    selector: 'ur-pin-code-input',
    imports: [ReactiveFormsModule, URHelperMessageComponent],
    providers: [TranslateService],
    templateUrl: './ur-pin-code-input.component.html',
    styleUrl: './ur-pin-code-input.component.scss',
})
export class UrPinCodeInputComponent implements OnInit, AfterViewInit {
    private readonly formBuilder = inject(FormBuilder);
    public readonly confirmationCode = this.formBuilder.group({ digits: this.formBuilder.array([]) });
    public inputModel: number[] = [];

    public numberOfCharacters = input(4);
    public confirmationCodeErrorMessage = input('');
    public isConfirmationCodeFailed = input(false);
    public autoFocus = input(true);
    public label = input('');

    public pinCode = model<string>();

    private readonly inputs = viewChildren<ElementRef<HTMLInputElement>>('inputs');

    public get digits(): FormArray {
        return this.confirmationCode.get('digits') as FormArray;
    }

    ngOnInit(): void {
        this.inputModel = Array.from({ length: this.numberOfCharacters() }, (_, i) => i);
        this.digits.clear(); // Clear the form array
        for (let i = 0; i < this.numberOfCharacters(); i++) {
            const control = this.formBuilder.control({ value: '', disabled: false }, [
                Validators.required,
                Validators.maxLength(1),
                NoWhiteSpaceValidator,
            ]);
            this.digits.push(control);
        }
        this.digits.valueChanges.pipe(debounceTime(500)).subscribe((value) => {
            if (this.digits.status === 'VALID') {
                const verificationCodeInputValue = value.join('');
                if (verificationCodeInputValue?.length === this.numberOfCharacters()) {
                    this.pinCode.set(verificationCodeInputValue);
                }
            }
        });
    }

    private async focus(): Promise<void> {
        return new Promise((resolve) =>
            setTimeout(() => {
                this.inputs()[0]?.nativeElement.focus();
                this.inputs()[0]?.nativeElement.select();
                resolve();
            }, 0),
        );
    }

    async ngAfterViewInit(): Promise<void> {
        if (this.autoFocus()) {
            await this.focus();
        }
    }

    onInput($event: any, formControl: AbstractControl, currentIndex: number): void {
        if (!$event.target.value.length) {
            return;
        }

        const inputLength = $event.target.value.length;
        if (inputLength > 0) {
            this.mapInputValuesToFormControlValues($event, formControl, currentIndex);
        }

        this.focusNextInput($event, currentIndex);
    }

    mapInputValuesToFormControlValues($event: any, formControl: AbstractControl, currentIndex: number): void {
        const inputValues = $event.target.value.split('');

        inputValues.forEach((value: any, valueIndex: number) => {
            const nextValueIndex = currentIndex + valueIndex;

            if (nextValueIndex > this.numberOfCharacters()) {
                return;
            }

            formControl.setValue(value);
        });

        currentIndex += inputValues.length - 2;
    }

    focusNextInput($event: any, currentIndex: number): void {
        const nextIndex = currentIndex + 1;
        if (nextIndex <= this.numberOfCharacters() - 1) {
            $event.target.nextElementSibling.focus();
        }
    }

    onKeyDown(event: any, currentIndex: number): void {
        const { key, target } = event;

        const previousIndex = currentIndex - 1;
        const hasPreviousIndex = previousIndex >= 0;
        switch (key) {
            case 'Backspace':
            case 'Delete':
                this.handleBackspaceOrDelete(target, hasPreviousIndex, currentIndex);
                break;
            case 'ArrowLeft':
                this.handleArrowLeft(target, hasPreviousIndex);
                break;
            case 'ArrowRight':
                this.handleArrowRight(target, currentIndex);
                break;
            default:
                break;
        }
    }

    handleBackspaceOrDelete(target: any, hasPreviousIndex: boolean, currentIndex: number): void {
        if (!target.value.length && hasPreviousIndex && currentIndex !== 0) {
            target.previousElementSibling.value = null;
            target.previousElementSibling.focus();
        }
    }

    handleArrowLeft(target: any, hasPreviousIndex: boolean): void {
        if (hasPreviousIndex) {
            target.previousElementSibling.focus();
        }
    }

    handleArrowRight(target: any, currentIndex: number): void {
        if (currentIndex < this.numberOfCharacters() - 1) {
            target.nextElementSibling.focus();
        }
    }

    onKeypressNumberOnly(event: any): boolean {
        const charCode = event.which ? event.which : event.keyCode;
        if (charCode > 31 && (charCode < 48 || charCode > 57)) {
            return false;
        }
        return true;
    }
}
