import { Component, HostListener, Input, ViewChild } from '@angular/core';
import { URButtonComponent } from '../Button/ur-button.component';
import { URIconButtonComponent } from '../IconButton/ur-icon-button.component';
import { URPopoverContentComponent } from '../Popover/PopoverContent/ur-popover-content.component';
import { URPopoverContentType } from '../Popover/ur-popover-content-type';
import { URPopoverDirective } from '../Popover/ur-popover.directive';
import { URContextMenuOptionComponent } from './ContextMenuOption/ur-context-menu-option.component';

/**
 * Context menu is used to show navigational items in a small popover menu.
 */
@Component({
    selector: 'ur-context-menu',
    templateUrl: './ur-context-menu.component.html',
    styleUrls: ['./ur-context-menu.component.scss'],
    imports: [URIconButtonComponent, URPopoverDirective, URButtonComponent, URPopoverContentComponent, URContextMenuOptionComponent],
})
export class URContextMenuComponent {
    @ViewChild(URPopoverDirective)
    popover?: URPopoverDirective;

    readonly URPopoverContentType = URPopoverContentType;

    /** Disable the background color */
    @Input()
    ghost = false;

    /** Icon on the trigger */
    @Input()
    icon = 'dots-three-list';

    /** Disable the possibility to open the menu */
    @Input()
    disabled = false;

    @Input()
    label?: string;

    @HostListener('click', ['$event'])
    public hostClick(event: MouseEvent): void {
        // We need to override the button hostClick to ensure the event do not propagate,
        event.stopPropagation();
    }

    close() {
        this.popover?.hide();
    }
}
