import { animate, AnimationTriggerMetadata, state, style, transition, trigger } from '@angular/animations';

export const EXPANSION_PANEL_ANIMATION_TIMING = '225ms ease-in-out';
const COLLAPSED_STATE = 'collapsed, void';
const EXPANDED_STATE = 'expanded';
const TRANSITION = 'expanded <=> collapsed, void => collapsed';
const EXPANDED_STYLE = { height: '*', visibility: 'visible' };
const COLLAPSED_STYLE = { height: '0px', visibility: 'hidden' };

export const urExpansionAnimations: {
    readonly indicatorRotate: AnimationTriggerMetadata;
    readonly indicatorRotateInverse: AnimationTriggerMetadata;
    readonly subTitleExpansion: AnimationTriggerMetadata;
    readonly bodyExpansion: AnimationTriggerMetadata;
} = {
    /** Animation that rotates the indicator arrow. */
    indicatorRotate: trigger('indicatorRotate', [
        state(COLLAPSED_STATE, style({ transform: 'rotate(0deg)' })),
        state(EXPANDED_STATE, style({ transform: 'rotate(90deg)' })),
        transition(TRANSITION, animate(EXPANSION_PANEL_ANIMATION_TIMING)),
    ]),
    indicatorRotateInverse: trigger('indicatorRotate', [
        state(COLLAPSED_STATE, style({ transform: 'rotate(0deg)' })),
        state(EXPANDED_STATE, style({ transform: 'rotate(90deg)' })),
        transition(TRANSITION, animate(EXPANSION_PANEL_ANIMATION_TIMING)),
    ]),

    subTitleExpansion: trigger('subTitleExpansion', [
        state(COLLAPSED_STATE, style(EXPANDED_STYLE)),
        state(EXPANDED_STATE, style(COLLAPSED_STYLE)),
        transition(TRANSITION, animate(EXPANSION_PANEL_ANIMATION_TIMING)),
    ]),
    /** Animation that expands and collapses the panel content. */
    bodyExpansion: trigger('bodyExpansion', [
        state(COLLAPSED_STATE, style(COLLAPSED_STYLE)),
        state(EXPANDED_STATE, style(EXPANDED_STYLE)),
        transition(TRANSITION, animate(EXPANSION_PANEL_ANIMATION_TIMING)),
    ]),
};
