import { NgClass } from '@angular/common';
import { Component, Input } from '@angular/core';

@Component({
    selector: 'ur-character-limit',
    templateUrl: './ur-character-limit.component.html',
    styleUrls: ['./ur-character-limit.component.scss'],
    imports: [NgClass],
})
export class URCharacterLimitComponent {
    /**
     * type of message
     */
    @Input()
    type: 'error' | 'info' = 'info';

    /**
     * character count
     */
    @Input()
    count = 0;

    /**
     * character limit
     */
    @Input()
    limit = 0;
}
