import { AfterViewInit, Directive, ElementRef, Input, NgZone, OnDestroy, Renderer2, ViewContainerRef } from '@angular/core';
import { FocusTrap, createFocusTrap } from 'focus-trap';
import { first } from 'rxjs/operators';
import { URPopoverContentComponent } from './PopoverContent/ur-popover-content.component';

@Directive({
    selector: '[urPopover]',
    exportAs: 'urPopover',
    standalone: true,
})
export class URPopoverDirective implements AfterViewInit, OnDestroy {
    initialized = false;

    @Input('urPopover')
    set popoverContentComponent(newComponent: URPopoverContentComponent) {
        if (newComponent === this._popoverContentComponent) {
            return;
        }
        this._popoverContentComponent = newComponent;
    }

    get popoverContentComponent(): URPopoverContentComponent | undefined {
        return this._popoverContentComponent;
    }

    @Input()
    showOnStart = false;

    @Input()
    set showPopover(value: boolean) {
        // because setters starts before ngAfterViewInit
        // only consider this trigger after the directive is initialized
        if (!this.initialized) {
            return;
        }

        if (value) {
            this.show();
        } else {
            this.hide();
        }
    }

    @Input()
    popoverData: unknown;

    @Input()
    disabled = false;

    private _popoverContentComponent?: URPopoverContentComponent;
    private _shown = false;
    private _focusTrap?: FocusTrap;

    constructor(
        private readonly _viewContainerRef: ViewContainerRef,
        private readonly _elementRef: ElementRef,
        private readonly _renderer: Renderer2,
        private readonly _ngZone: NgZone,
    ) {}

    ngAfterViewInit() {
        this._renderer.listen(this._elementRef.nativeElement, 'click', this.toggle.bind(this));
        if (this.showOnStart) {
            this._ngZone.onStable.pipe(first()).subscribe(() => this.show());
        }

        this.initialized = true;
    }

    ngOnDestroy(): void {
        this._ngZone.runOutsideAngular(() => {
            if ((window as any)?.__trapStack) {
                const ft = (window as any)?.__trapStack.pop();
                ft?.deactivate();
            }
        });
    }

    toggle($event: MouseEvent | null = null) {
        $event?.stopPropagation();
        const element = this._viewContainerRef?.element?.nativeElement?.firstChild;

        if (element?.disabled || this.disabled) {
            return;
        }
        this._shown ? this.hide() : this.show();
    }

    show() {
        if (this._shown) {
            return;
        }
        this._shown = true;
        const popoverRef = this._popoverContentComponent;
        const element = this._viewContainerRef.element.nativeElement;
        if (popoverRef) {
            if (popoverRef?.anchorElement !== element) {
                popoverRef.anchorElement = element;
            }

            popoverRef.popoverData = this.popoverData;
            popoverRef.show();

            this._enableFocusTrap(popoverRef._elementRef.nativeElement);
        }
    }

    hide() {
        if (!this._shown) {
            return;
        }
        this._shown = false;
        this._focusTrap?.deactivate();
        this._popoverContentComponent?.hide();
    }

    private isTargetInElement(selector: string, event: Event) {
        return document.querySelector(selector)?.contains(event?.composedPath()?.[0] as Node);
    }

    private _enableFocusTrap(element: HTMLElement) {
        const options = {
            allowOutsideClick: (event: Event) => {
                const isTargetInAnchor = this._viewContainerRef?.element?.nativeElement?.contains(event.composedPath()[0]);
                const isTargetInKeyboard = this.isTargetInElement('.keyboard-main-container', event);
                const isTargetInShowKeyboardButton = this.isTargetInElement('.show-keyboard-button', event);
                const isTargetOnErrorRenderer = this.isTargetInElement('.ur-dialog-renderer', event);
                const isTargetOnDropdownRenderer = this.isTargetInElement('.ng-dropdown-panel', event);
                if (
                    !isTargetInAnchor &&
                    !isTargetInKeyboard &&
                    !isTargetInShowKeyboardButton &&
                    !isTargetOnErrorRenderer &&
                    !isTargetOnDropdownRenderer
                ) {
                    this.hide();
                }
                return true;
            },
            initialFocus: this._viewContainerRef?.element?.nativeElement,
            fallbackFocus: this._viewContainerRef?.element?.nativeElement,
            trapStack: (window as any)?.__trapStack,
        };
        this._ngZone.runOutsideAngular(() => {
            this._focusTrap = createFocusTrap(element, options);
            this._focusTrap?.activate();
        });
    }
}
