import { Component, ElementRef, HostBinding, Input, ViewChild } from '@angular/core';

type URCardState = 'default' | 'warning';

@Component({
    selector: 'ur-card',
    templateUrl: './ur-card.component.html',
    styleUrls: ['./ur-card.component.scss'],
    imports: [],
})
export class URCardComponent {
    @ViewChild('iconWrapper') iconWrapper?: ElementRef<HTMLDivElement>;
    @Input() headline?: string;
    @Input() supportiveText?: string;
    @Input() description?: string;
    @Input() state?: URCardState = 'default';
    @Input() disabled?: boolean;

    @HostBinding('class') get urCardClass() {
        return 'ur-card';
    }

    @HostBinding('class.disabled') get disabledClass() {
        return this.disabled;
    }

    @HostBinding('class.warning') get warningClass() {
        return this.state === 'warning';
    }
}
