import { animate, style, transition, trigger } from '@angular/animations';
import { LowerCasePipe } from '@angular/common';
import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    HostListener,
    Inject,
    OnInit,
    Output,
    ViewChild,
} from '@angular/core';
import { TranslateService, TranslateModule } from '@ngx-translate/core';
import { Key } from 'ts-key-enum';
import {
    CloseReason,
    CloseReasonType,
    DropdownOption,
    InputTranslation,
    SelectedInput,
    SelectedType,
    TabInputData,
    TabInputInsertableModel,
    TabInputReturnData,
    TabInputValue,
} from '@universal-robots/ui-models';
import { DATA_TOKEN, DialogRef } from '../../../ref';
import { uniqueAlphaNumericId } from '../../../utils/uniqueAlphaNumericId';
import { URButtonComponent } from '../../Button/ur-button.component';
import { URGenericDialogComponent } from '../../Dialogs/GenericDialog/ur-generic-dialog.component';
import { URDropdownComponent } from '../../Dropdown/ur-dropdown.component';
import { URHeadingComponent } from '../../Heading/ur-heading.component';
import { URIconButtonComponent } from '../../IconButton/ur-icon-button.component';
import { URLabelComponent } from '../../Label/ur-label.component';
import { URTabComponent } from '../../Tab/ur-tab.component';
import { URTabsComponent } from '../../Tabs/ur-tabs.component';
import { URInputComponent } from '../Input/ur-input.component';
import { URNumberInputComponent } from '../NumericField/ur-number-input.component';
import { URQuickAccessComponent } from './QuickAccess/ur-quick-access.component';
import { URUtilityButtonsComponent } from './UtilityButtons/ur-utility-buttons.component';

@Component({
    selector: 'ur-tab-input-container',
    templateUrl: './ur-tab-input-container.component.html',
    styleUrls: ['./ur-tab-input-container.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [
        URGenericDialogComponent,
        URLabelComponent,
        URTabsComponent,
        URTabComponent,
        URInputComponent,
        URNumberInputComponent,
        URDropdownComponent,
        LowerCasePipe,
        TranslateModule,
        URHeadingComponent,
        URIconButtonComponent,
        URButtonComponent,
        URQuickAccessComponent,
        URUtilityButtonsComponent,
    ],
    animations: [
        trigger('expandWidth', [
            transition(':enter', [
                style({ width: '0px', 'padding-left': '0px', 'padding-right': '0px', overflow: 'hidden' }),
                animate('200ms cubic-bezier(.11,.11,.52,.99)', style({ width: '*', 'padding-left': '*', 'padding-right': '*' })),
            ]),
            transition(':leave', [
                style({ width: '*', 'padding-left': '*', 'padding-right': '*', overflow: 'hidden' }),
                animate('200ms cubic-bezier(.11,.11,.52,.99)', style({ width: '0px', 'padding-left': '0px', 'padding-right': '0px' })),
            ]),
        ]),
    ],
})
export class URTabInputContainerComponent implements OnInit {
    @ViewChild('textInput') textInput?: URInputComponent;
    @ViewChild('numberInput') numberInput?: URInputComponent;
    @ViewChild('expressionInput') expressionInput?: URInputComponent;
    @ViewChild('nav') navbar?: URTabsComponent;
    @ViewChild('variableInput') variableInput?: URDropdownComponent;
    @ViewChild('variableLabel') variableLabel?: URLabelComponent;

    CloseReason = CloseReason;
    uid = uniqueAlphaNumericId();
    selectedType: SelectedType;
    label?: string;
    variableNameList: DropdownOption[] = [];
    functionsNameList: DropdownOption[] = [];
    inputValue: string | number = '';
    expressionValue: string | number = '';
    variableValue?: DropdownOption;
    valueInfo = '';
    isValid = false;
    expressionInfo = '';
    previousValue: TabInputValue = { ...this.data.tabInputValue };
    placeholder?: string;
    expressionTranslations: InputTranslation;
    insertable?: TabInputInsertableModel;

    constructor(
        readonly cd: ChangeDetectorRef,
        protected readonly translate: TranslateService,
        protected readonly element: ElementRef,
        @Inject(DATA_TOKEN) public data: TabInputData,
        private readonly dialogRef: DialogRef<TabInputReturnData>,
    ) {
        this.initializeTranslations();
        this.setSelectedType();
        this.generateMessage();

        this.expressionTranslations = {
            requiredValidationMessage: this.translate.instant('ui.validation.tab_input.required'),
            tooLongMessage: this.translate.instant('ui.validation.tab_input.max_length'),
        };
        this.previousValue = { ...this.data.tabInputValue };

        if (this.data.tabs.length === 1) {
            this.data.tabInputValue.selectedType = this.data.tabs[0];
        }
        this.variableNameList = this.getVariableNameList();
        this.selectedType = this.data.tabInputValue.selectedType;
        this.insertable = this.data.insertables;
    }

    @Output()
    @HostListener('document:keydown', ['$event'])
    onKeydownHandler(event: KeyboardEvent) {
        if (this.element.nativeElement.matches(':focus-within')) {
            event.stopPropagation();
            event.stopImmediatePropagation();

            const currInput = this.currentInput();

            const changesMade = this.currentValue() !== this.previousValue?.value;
            const validInputChange = currInput !== undefined && currInput.valid;
            const validVariableChange = this.selectedType === SelectedInput.VARIABLE && this.variableValid(this.variableValue?.label);

            // On Enter close dialog with current value saved
            if (event.key === Key.Enter && changesMade && (validInputChange || validVariableChange)) {
                this.onDialogClose({ $event: event, reason: CloseReason.CONFIRMED });
            }
        }
    }

    ngOnInit(): void {
        this.updatePlaceholderText();
        this.setPreviousInputVal();
        this.setInitialValues();

        this.cd.detectChanges();
    }

    setPreviousInputVal(): void {
        if (!this.previousValue.selectedType) {
            this.inputValue = 0;
        } else if (this.previousValue.selectedType === SelectedInput.VALUE) {
            this.inputValue = this.previousValue.value;
        }
    }

    private setInitialValues(): void {
        const { selectedType, value } = this.previousValue;

        switch (selectedType) {
            case SelectedInput.VALUE:
                this.inputValue = value;
                break;
            case SelectedInput.VARIABLE:
                if (value !== undefined) {
                    this.variableValue = {
                        label: value.toString(),
                        invalid: true,
                    };
                }
                break;
            default:
                this.expressionValue = value ?? '';
        }
    }
    private initializeTranslations(): void {
        this.expressionTranslations = {
            requiredValidationMessage: this.translate.instant('ui.validation.tab_input.required'),
            tooLongMessage: this.translate.instant('ui.validation.tab_input.max_length'),
        };
    }

    private setSelectedType(): void {
        if (this.data.tabs.length === 1) {
            this.data.tabInputValue.selectedType = this.data.tabs[0];
        }
    }

    private generateMessage() {
        this.valueInfo = '';
        this.expressionInfo = '';

        switch (this.previousValue.selectedType) {
            case SelectedInput.VALUE:
                this.valueInfo = this.translate.instant('app.current_value', { value: this.previousValue.value });
                break;
            case SelectedInput.EXPRESSION:
                this.expressionInfo = this.translate.instant('app.current_value', { value: this.previousValue.value });
                break;
        }
    }

    currentInput() {
        this.cd.detectChanges();
        switch (this.selectedType) {
            case SelectedInput.VALUE:
                return this.data.valueType === 'string' ? this.textInput : this.numberInput;
            case SelectedInput.EXPRESSION:
                return this.expressionInput;
            default:
                return undefined;
        }
    }

    onLinkChanged(newSelectedType: SelectedType) {
        this.updatePlaceholderText();
        this.selectedType = newSelectedType;
        if (this.selectedType === SelectedInput.VARIABLE) {
            this.isValid = this.variableValid(this.variableValue?.label);
        }
    }

    onInputValueChange(value: string | number) {
        this.inputValue = value;
    }

    onSetVariable(variableName: string) {
        if (variableName) {
            this.variableValue = { label: variableName, invalid: this.variableValid(variableName) };
            this.isValid = this.variableValid(variableName);
        }
    }

    onExpressionValueChange(value: string | number) {
        this.expressionValue = value;
    }

    currentValue() {
        switch (this.selectedType) {
            case SelectedInput.VALUE:
                if (this.data.valueType === 'string' && this.textInput) {
                    return this.textInput.value;
                } else if (this.data.valueType !== 'string' && this.numberInput) {
                    return this.numberInput.value;
                } else {
                    return this.previousValue.value;
                }
            case SelectedInput.EXPRESSION:
                return this.expressionValue;
            case SelectedInput.VARIABLE:
                return (this.variableInput?.selectedOption as DropdownOption).label;
            default:
                return this.previousValue.value;
        }
    }

    onDialogClose({ reason }: { $event?: Event | KeyboardEvent; reason: CloseReasonType }) {
        if (reason === CloseReason.CONFIRMED) {
            const returnValue: TabInputValue = {
                value: this.currentValue(),
                selectedType: this.selectedType,
            };
            this.data.tabInputValue = returnValue;
            this.dialogRef.close({
                reason: CloseReason.CONFIRMED,
                returnData: {
                    ...returnValue,
                    valid: this.isValid,
                },
            });
        } else if (reason === CloseReason.CANCELLED) {
            this.dialogRef.close({ reason });
        }
    }

    onVariableSelect(variableName: string) {
        this.expressionValue = `${this.expressionValue}${variableName}`;
        // for evaluating input is valid we need to wait for the value to be set
        requestAnimationFrame(() => {
            this.expressionInput?.checkValidators();
            this.expressionInput?.focus();
        });
    }

    onFunctionSelected(functionName: string) {
        this.expressionAdded(functionName);
    }

    onBuiltInSelected(builtInName: string) {
        this.expressionAdded(builtInName);
    }

    onQuickAccessSelected(name: string) {
        this.expressionAdded(name);
    }

    variableValid(variableName: string | undefined) {
        const variable = this.data?.variables?.find((tmpVar) => tmpVar.name === variableName);
        return !!variable && !variable.suppressed;
    }

    updatePlaceholderText() {
        this.placeholder = `${this.previousValue.selectedType}: ${this.previousValue.value ?? ''}`;
    }

    public getVariableNameList() {
        return (
            this.data.variables
                ?.filter((varObj) =>
                    Array.isArray(this.data.valueType)
                        ? this.data.valueType.some((valType) => valType === varObj.valueType)
                        : this.data.valueType === varObj.valueType,
                )
                .map((item) => ({ label: item.name, invalid: !!item.suppressed })) ?? []
        );
    }

    public getLabel(key: string): string {
        // Add optional extra text (if any) to the default input label:
        return this.translate.instant(key) + (this.data.inputLabel ? ` (${this.data.inputLabel})` : '');
    }

    onValidChanged(valid: boolean) {
        this.isValid = valid;
    }

    expressionAdded(utilityExpression: string) {
        const expressionAsString = this.expressionValue.toString();
        const spacing = expressionAsString.endsWith(' ') || expressionAsString.length === 0 ? '' : ' ';

        this.onExpressionValueChange(expressionAsString + spacing + utilityExpression);

        // for evaluating input is valid we need to wait for the value to be set
        requestAnimationFrame(() => {
            this.expressionInput?.checkValidators();
            this.expressionInput?.focus();
        });
    }
}
