import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    DoCheck,
    EventEmitter,
    HostBinding,
    Input,
    NgZone,
    OnChanges,
    OnInit,
    Output,
    SimpleChanges,
    ViewChild,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { firstValueFrom } from 'rxjs';
import { CloseDialog, CloseReason, DialogInputData, DialogInputReturnData, InputTranslation } from '@universal-robots/ui-models';
import { URPortalService } from '../../../services/ur-portal.service';
import { uniqueAlphaNumericId } from '../../../utils/uniqueAlphaNumericId';
import { URDialogInputContainerComponent } from '../DialogInputContainer/ur-dialog-input-container.component';
import { URFieldTriggerComponent } from '../FieldTrigger/ur-field-trigger.component';
import { URField } from '../ur-field';

interface RealAnimationEvent extends AnimationEvent {
    animationName: string;
}
/**
 * Dialog-input is the main way to get native text and number input fields. The component automatically creates opens and close a dialog and returns the new or edited value.
 */
@Component({
    selector: 'ur-dialog-input',
    templateUrl: './ur-dialog-input.component.html',
    styleUrls: ['./ur-dialog-input.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URFieldTriggerComponent],
})
export class URDialogInputComponent extends URField implements OnInit, OnChanges, DoCheck {
    uid = uniqueAlphaNumericId();

    /* if input is of the type password */
    isPassword = false;
    @ViewChild('triggerButton') triggerButton?: URFieldTriggerComponent;
    @HostBinding('attr.disabled') get disabledString() {
        return this.disabled ? 'disabled' : null;
    }

    /*
     * Declaring how much the value should increment or decrement
     * */
    @Input() step = 0;
    /**
     * Number of digits allowed in the input field
     */
    @Input() digitLimit = 10;

    /* Controls if the input only allows positive numbers  */
    @Input() positiveDigitsOnly = false;

    @Input() precision = 0;
    @Input() override translations: InputTranslation = {};
    @Input() rendererId?: string;
    @Input() title?: string;
    /**
     * onBeforeClose is being invoked when the user hit confirm and before before the dialog closes.
     * The function works the same way as a inputValidator fn but can also be used to other purposes
     */
    @Input()
    onBeforeClose?: (value: string) => string | null | Promise<string | null>;

    @Output()
    handleClick: EventEmitter<Event> = new EventEmitter();

    constructor(
        private readonly dialogService: URPortalService,
        protected override readonly translate: TranslateService,
        private readonly ngZone: NgZone,
        protected override readonly cd: ChangeDetectorRef,
    ) {
        super(translate, cd);
    }

    async ngOnInit() {
        await this.checkValidators(this.value);
        if (this.type === 'password') {
            this.isPassword = true;
            this.cd.detectChanges();
        }
    }

    async ngOnChanges(changes: SimpleChanges) {
        if (changes['validators']) {
            await this.checkValidators(this.value);
        }
    }

    async ngDoCheck() {
        // check the native validation
        if (this.checkValidity() !== this.valid) {
            await this.checkValidators();
        }
    }

    onCloseCallback = ({ reason, returnData }: CloseDialog<DialogInputReturnData>) => {
        // if dialog isn't cancel, save data and emit valueChanged event
        if (reason === CloseReason.CONFIRMED && returnData?.value !== null && returnData?.value !== undefined) {
            if (this.value !== returnData.value) {
                this.value = returnData.value;
                this.valueChanged.emit(this.value);
                this.cd.detectChanges();
            }
        }
        this.ngZone.runOutsideAngular(() => {
            requestAnimationFrame(() => {
                this.triggerButton?.setFocus();
            });
        });
    };

    async onClick(event: Event) {
        event.preventDefault();
        if (this.disabled) {
            return;
        }
        this.handleClick.emit(event);
        const title =
            this.title || this.value?.toString().length
                ? this.translate.instant('ui.dialog_input.edit')
                : this.translate.instant('ui.dialog_input.create');
        if (!this.disabled && !this.readOnly) {
            const dialogRef = this.dialogService.open<DialogInputReturnData, DialogInputData>(URDialogInputContainerComponent, {
                label: this.label,
                title: title,
                message: this.message,
                currentValue: this.currentValue,
                placeholder: this.placeholder,
                type: this.type,
                value: this.value,
                validators: this.validators,
                validation: this.validation(),
                valid: this.valid,
                unit: this.unit,
                uid: this.uid,
                precision: this.precision,
                keyboardLayout: this.keyboardLayout,
                step: this.step,
                positiveDigitsOnly: this.positiveDigitsOnly,
                showClearIcon: this.showClearIcon,
                translations: this.translations,
                onBeforeClose: this.onBeforeClose,
            });
            try {
                const value = await firstValueFrom(dialogRef.afterClose);
                this.onCloseCallback(value);
            } catch (e) {
                return;
            } finally {
                this.cd.detectChanges();
            }
        }
    }
}
