import { Directive, ElementRef, Input } from '@angular/core';

@Directive({
    selector: '[urAutoFocus]',
})
export class URAutoFocusDirective {
    @Input()
    set urAutoFocus(focus: boolean) {
        if (focus) {
            // need next tick when used inside a dialog
            setTimeout(() => this.hostElement.nativeElement.focus(), 0);
        }
    }
    constructor(private hostElement: ElementRef) {}
}
@Directive({
    selector: '[urAutoFocusAndSelect]',
})
export class URAutoFocusAndSelectDirective {
    @Input()
    set urAutoFocusAndSelect(focus: boolean) {
        if (focus) {
            // need next tick when used inside a dialog
            setTimeout(() => {
                this.hostElement.nativeElement.select();
                this.hostElement.nativeElement.focus();
            }, 1);
        }
    }
    constructor(private hostElement: ElementRef) {}
}
