import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    ViewChild,
    input,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
    InputTranslation,
    InputType,
    InputValidationAttribute,
    InputValidator,
    NumberInputType,
    VirtualKeyboardLayout,
} from '@universal-robots/ui-models';

export enum ValidationIcon {
    Warning = 'warning',
    Invalid = 'error',
    None = 'none',
}
@Component({
    selector: 'ur-field',
    template: '',
    changeDetection: ChangeDetectionStrategy.OnPush,
    standalone: true,
})
export class URField {
    private _currentMessage = '';

    /**
     * currently displayed message text below input box
     */
    get currentMessage(): string {
        return this._currentMessage;
    }
    protected set currentMessage(value: string) {
        this._currentMessage = value;
    }
    hasFocus = false;
    validationIcon: ValidationIcon = ValidationIcon.None;

    /**
     * will autofocus the input field when it's rendered
     */
    @Input()
    autoFocus = false;
    /**
     * override the default keyboard layout
     */
    @Input()
    keyboardLayout: VirtualKeyboardLayout = 'alpha-numeric';
    /**
     * the maximum character limit for the input value
     * @deprecated - please use the validation.maxLength
     */
    @Input()
    charLimit = 0;
    /**
     * type of input field
     */
    @Input()
    type: InputType | NumberInputType = 'text';

    /**
     * to disable the field
     */
    @Input()
    disabled = false;
    /**
     * to force the display of the pencil icon even if disabled (eg for popovers)
     * * @deprecated
     */
    @Input()
    forcePencilIcon = false;
    /**
     * unique id for the input field
     */
    @Input()
    inputId?: string;
    /**
     * label to describe the field type
     */
    @Input()
    label = '';
    /**
     * helper message to guide the user of the current state or purpose of the field
     */
    @Input()
    message = '';
    /**
     * placeholder for when no value
     */
    @Input()
    placeholder = '';
    /**
     * readonly attribute
     */
    @Input()
    readOnly = false;
    /**
     * whether to show the clear field icon
     */
    @Input()
    showClearIcon = false;
    /**
     * whether to show the edit field pencil icon
     * @deprecated
     */
    @Input()
    showPencilIcon = true;
    /**
     * list of validators to check input against
     *   A validator is a method that takes in an input string
     *   and returns an error message string if the input is not valid
     *   OR null if the input is valid.
     */
    @Input()
    validators: InputValidator[] = [];
    /**
     * obj of native validation methods to check input against
     */
    validation = input<InputValidationAttribute>({ required: false, type: false });

    private _valid = true;

    protected isWarning = false;
    public setIsWarning(value: boolean) {
        this.isWarning = value;
        this.cd.detectChanges();
    }

    /**
     * whether the field is valid or not
     */
    get valid(): boolean {
        return this._valid;
    }

    protected set valid(value: boolean) {
        this._valid = value;
    }

    private _error = false;

    /*
     * Determines if the state of the field has an error or not.
     * */
    get error(): boolean {
        return this._error;
    }

    /**
     * size for the input field
     * @deprecated
     */
    @Input()
    width = null;
    /**
     * unit placeholder
     */
    @Input()
    unit = '';
    /**
     * validation error messages
     */
    @Input()
    translations: InputTranslation = {};
    /**
     * show a current value as message
     */
    @Input()
    currentValue?: string | number;

    /**
     * on change event listener
     */
    @Output()
    handleChange: EventEmitter<number | string> = new EventEmitter();
    /**
     * on input field valid change event listener
     */
    @Output()
    validChanged: EventEmitter<boolean> = new EventEmitter();
    /**
     * on input field value change event listener
     */
    @Output()
    valueChanged: EventEmitter<number | string> = new EventEmitter();
    /**
     * on input field blur event listener
     */
    @Output()
    blurred: EventEmitter<Event> = new EventEmitter();
    /**
     * on input field focus event listener
     */
    @Output()
    focused: EventEmitter<Event> = new EventEmitter();
    /**
     * reference to the internal input field
     */
    @ViewChild('input') protected input?: ElementRef<HTMLInputElement>;

    /**
     * value of the field
     */
    _value = '';

    public get value() {
        return this._value;
    }

    @Input()
    public set value(value: string) {
        if (typeof value !== 'undefined') {
            this._value = value;
            this.checkValidators(value).then();
            this.cd.detectChanges();
        }
    }
    constructor(
        protected readonly translate: TranslateService,
        protected readonly cd: ChangeDetectorRef,
    ) {}
    /**
     * getter for showing or hiding validation icon
     */
    get showValidationIcon() {
        return this.validationIcon !== ValidationIcon.None && !this.showClearIcon && this.type !== 'password';
    }

    onFocus(event: Event) {
        this.hasFocus = true;
        this.focused.emit(event);
        this.cd.detectChanges();
    }

    onBlur(event: Event) {
        this.hasFocus = false;
        this.blurred.emit(event);
        this.cd.detectChanges();
    }

    onChange(event: Event) {
        const value = (event.target as HTMLInputElement)?.value;
        this.handleChange.emit(value);
    }

    checkValueMissing(valueMissing: boolean) {
        if (this.validation().required && valueMissing) {
            this.currentMessage = this.translations?.requiredValidationMessage || this.translate.instant('app.validation.value_missing');
            this.validationIcon = ValidationIcon.None;
            this.cd.detectChanges();
        }
    }

    checkMaxLength(tooLong: boolean) {
        if (this.validation().maxLength && tooLong) {
            this.currentMessage =
                this.translations?.tooLongMessage || this.translate.instant('app.validation.max_length', { value: this.value });
            this.cd.detectChanges();
        }
    }

    checkMinLength(tooShort: boolean) {
        if (this.validation()?.minLength && tooShort) {
            this.currentMessage =
                this.translations?.tooShortMessage || this.translate.instant('app.validation.min_length', { value: this.value });
            this.cd.detectChanges();
        }
    }

    checkTypeMismatch(typeMismatch: boolean) {
        if (this.validation()?.type && typeMismatch) {
            this.currentMessage = this.translations?.typeMismatchMessage || this.translate.instant('app.validation.type_mismatch');
            this.cd.detectChanges();
        }
    }
    checkPatternMismatch(patternMismatch: boolean) {
        if (this.validation()?.pattern && patternMismatch) {
            this.currentMessage = this.translations?.patternMismatchMessage || this.translate.instant('app.validation.patter_mismatch');
            this.cd.detectChanges();
        }
    }
    checkValidity(): boolean {
        let valid = true;
        const elementValidity = this.input?.nativeElement?.validity;

        if (elementValidity && !elementValidity.valid) {
            const { valueMissing, tooLong, tooShort, typeMismatch, patternMismatch } = elementValidity;
            valid = false;
            this.validationIcon = ValidationIcon.Invalid;

            // Required
            this.checkValueMissing(valueMissing);
            // maxLength
            this.checkMaxLength(tooLong);
            // minLength
            this.checkMinLength(tooShort);
            // type mismatch (email)
            this.checkTypeMismatch(typeMismatch);
            // pattern mismatch (regex)
            this.checkPatternMismatch(patternMismatch);
            this.cd.detectChanges();
        }
        return valid;
    }

    /**
     * validate input and set error message based on validators
     */
    async checkValidators(value?: string | number): Promise<boolean> {
        let valid = true;

        // fn validations is the highest priority and will on
        for (const validator of this.validators) {
            const errorMessage = await validator(value || this.value);
            if (typeof errorMessage === 'string') {
                valid = false;
                this._currentMessage = errorMessage;
                this.validationIcon = ValidationIcon.Invalid;
            } else if (typeof errorMessage === 'object' && errorMessage?.validationType === 'error') {
                valid = false;
                this._currentMessage = errorMessage.message;
                this.validationIcon = ValidationIcon.Invalid;
            } else if (typeof errorMessage === 'object' && errorMessage?.validationType === 'warning') {
                valid = true;
                this.isWarning = true;
                this._currentMessage = errorMessage.message;
                this.validationIcon = ValidationIcon.Warning;
                this.cd.detectChanges();
                if (valid !== this._valid) {
                    this._valid = valid;
                    this.validChanged.emit(valid);
                }
                return valid;
            }
        }
        // check for native validity
        if (valid) {
            valid = this.checkValidity();
        }
        if (valid !== this._valid) {
            this._valid = valid;
            this.validChanged.emit(valid);
        }
        if (valid) {
            this._currentMessage = this.getCurrentValue() || this.message;
            this.validationIcon = ValidationIcon.None;
        }
        this.isWarning = false;
        this.cd.detectChanges();
        return valid;
    }

    /**
     * clear the value of the input field
     * @param e clickEvent
     */
    clearValue(e?: Event): void {
        e?.preventDefault();
        this.value = '';
        this.valueChanged.emit(this.value);
        this.cd.detectChanges();
    }

    /**
     * this function is called on label click which means any click that happened
     * inside the label was propagated to the label and any click that
     * happens inside the label should not remove the focus from the label
     * for that reason we need to force focus on the input field
     * @returns {boolean}
     */
    checkFocus(): boolean {
        this.focus();
        return false;
    }

    /**
     * A way to call focus on the input field
     */
    focus(options: FocusOptions = {}): void {
        this.input?.nativeElement.focus(options);
        this.input?.nativeElement.setSelectionRange(this.value.length, this.value.length);
    }

    /**
     * current length of the input string
     */
    get charCount() {
        return this.value?.length ?? 0;
    }

    getCurrentValue() {
        return this.currentValue && this.currentValue.toString().length
            ? this.translate.instant('app.current_value', { value: this.currentValue })
            : null;
    }
    public setMessage({ message, valid }: { message: string; valid: boolean }) {
        this._currentMessage = message;
        this._valid = valid;
        this.cd.detectChanges();
    }
}
