import { Directive, ElementRef, HostListener, input } from '@angular/core';

@Directive({
    selector: '[urMaxLength]',
    standalone: true,
})
export class URMaxLengthDirective {
    urMaxLength = input<number>(11);

    constructor(private el: ElementRef<HTMLInputElement>) {}

    @HostListener('input', ['$event'])
    onInput(): void {
        if (!this.urMaxLength()) {
            return;
        }
        const input = this.el.nativeElement;
        if (input.value.length > this.urMaxLength()) {
            input.value = input.value.slice(0, this.urMaxLength());
            input.dispatchEvent(new Event('input'));
        }
    }

    @HostListener('paste', ['$event'])
    onPaste(event: ClipboardEvent): void {
        if (!this.urMaxLength()) {
            return;
        }
        const pastedValue = event.clipboardData?.getData('text');
        if (pastedValue && pastedValue.length > this.urMaxLength()) {
            event.preventDefault();
            const input = this.el.nativeElement;
            input.value = pastedValue.slice(0, this.urMaxLength());
            input.dispatchEvent(new Event('input'));
        }
    }
}
