import { Component, Inject } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfirmationDialog } from '@universal-robots/ui-models';
import { DATA_TOKEN } from '../../../ref';
import { URConfirmationDialogComponent } from '../ConfirmationDialog/ur-confirmation-dialog.component';

@Component({
    selector: 'ur-service-confirmation-dialog',
    template: `<ur-confirmation-dialog
        [title]="data.title"
        [icon]="data.icon"
        [size]="data.size"
        [cancelText]="cancelText"
        [confirmText]="confirmText"
        [showCancelButton]="data.showCancelButton ?? true"
        [critical]="data.critical"
    >
        <p [innerHtml]="data.text"></p>
    </ur-confirmation-dialog>`,
    imports: [URConfirmationDialogComponent],
})
export class URServiceConfirmationDialogComponent {
    cancelText: string;
    confirmText: string;
    constructor(
        private readonly translate: TranslateService,
        @Inject(DATA_TOKEN) public data: ConfirmationDialog,
    ) {
        this.cancelText = this.data.cancelText ?? this.translate.instant('app.cancel_button');
        this.confirmText = this.data.confirmText ?? this.translate.instant('app.confirm_button');
    }
}
