import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { URAutoFocusAndSelectDirective } from '../../../directives/focus.directive';
import { URCharacterLimitComponent } from '../../CharacterLimit/ur-character-limit.component';
import { URHelperMessageComponent } from '../../HelperMessage/ur-helper-message.component';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { URLabelComponent } from '../../Label/ur-label.component';
import { URField } from '../ur-field';

/**
 * The input component will handle all types except date, range and number types.
 */
@Component({
    selector: 'ur-input',
    templateUrl: './ur-input.component.html',
    styleUrls: ['./ur-input.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    imports: [URLabelComponent, URAutoFocusAndSelectDirective, URIconComponent, URHelperMessageComponent, URCharacterLimitComponent],
})
export class URInputComponent extends URField implements OnInit {
    public showPassword = false;
    constructor(
        protected override readonly translate: TranslateService,
        protected override readonly cd: ChangeDetectorRef,
    ) {
        super(translate, cd);
    }
    /**
     * check validators once immediately on startup
     */
    ngOnInit() {
        this.checkValidators(this.value);
        // emit the valid change on initial because between open and closed
        // if the valid state did not change it will never be emitted
        // timeout to avoid NG0100
        setTimeout(() => this.validChanged.emit(this.valid), 0);
    }

    /**
     * internal way to detect field value change
     */
    onInput(event: { target: { value: string } }) {
        this.value = event.target.value;
        this.valueChanged.emit(this.value);
        this.cd.detectChanges();
    }

    get clearable(): boolean {
        return !this.disabled && !this.readOnly && this.showClearIcon && this.value !== '' && !this.showValidationIcon;
    }
    public getValue() {
        return this.value;
    }

    displayPassword(e?: Event) {
        e?.preventDefault();
        if (this.type === 'password') {
            this.showPassword = !this.showPassword;
            this.cd.detectChanges();
        }
    }
}
