import { AnimationEvent } from '@angular/animations';
import { DialogConfig } from '@universal-robots/ui-models';

export const ANIMATION_TRIGGER = {
    ENTER: 'enter',
    PASSIVE: 'passive',
} as const;
export const ANIMATION_STATE = {
    HIDDEN: 'hidden',
    VISIBLE: 'visible',
    ACTIVE: 'active',
    PASSIVE: 'passive',
    VOID: 'void',
} as const;

export interface AnimationState {
    animation: string;
    state: string;
    config?: DialogConfig;
}
export interface DialogAnimationEvent extends AnimationState {
    activeDialogId: string;
}

export interface DialogAnimationState extends AnimationState {
    $event?: AnimationEvent;
}
