import { NgClass, NgTemplateOutlet } from '@angular/common';
import { Component, HostBinding, Input, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CloseReason, DialogSize, DialogSizeType } from '@universal-robots/ui-models';
import { scaleOnEnterAnimation, scaleOnPassiveAnimation } from '../../../animations';
import { DialogRef } from '../../../ref';
import { URButtonComponent } from '../../Button/ur-button.component';
import { URHeadingComponent } from '../../Heading/ur-heading.component';
import { URIconComponent } from '../../Icon/ur-icon.component';
import { URDialog } from '../ur-dialog';

@Component({
    selector: 'ur-confirmation-dialog',
    templateUrl: './ur-confirmation-dialog.component.html',
    styleUrls: ['./ur-confirmation-dialog.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: [scaleOnEnterAnimation, scaleOnPassiveAnimation],
    imports: [URIconComponent, URHeadingComponent, NgTemplateOutlet, URButtonComponent, NgClass],
})
export class URConfirmationDialogComponent extends URDialog {
    /**
     * dialog size enums
     */
    @Input()
    override size: DialogSizeType = DialogSize.DEFAULT;

    /**
     * show cancel button
     */
    @Input()
    showCancelButton = true;

    /**
     * cancel button label
     */
    @Input()
    cancelText: string;

    /**
     * confirm button label
     */
    @Input()
    confirmText: string;

    /**
     * disable confirm button
     */
    @Input()
    disableConfirm = false;

    @HostBinding('attr.role') override get role() {
        return 'dialog';
    }

    constructor(
        _dialogRef: DialogRef<unknown>,
        private readonly translate: TranslateService,
    ) {
        super(_dialogRef);
        this.cancelText = this.translate.instant('app.cancel_button');
        this.confirmText = this.translate.instant('app.confirm_button');
    }

    // force close the dialog
    public closeDialog(reason = CloseReason.CANCELLED) {
        this.dialogRef.close({ reason });
    }

    /**
     * close dialog on cancel, close-icon and esc
     * @param event
     */
    onCloseDialog(event: KeyboardEvent | Event) {
        event.stopPropagation();
        event.stopImmediatePropagation();
        this.dialogRef.close({ reason: CloseReason.CANCELLED });
    }
    /**
     * close dialog on confirmation click
     */
    onConfirmClick() {
        this.dialogRef.close({ reason: CloseReason.CONFIRMED });
    }
}
