import { Component, EventEmitter, Input, Output } from '@angular/core';
import { URIconComponent } from '../../Icon/ur-icon.component';

@Component({
    selector: 'ur-popover-box-button',
    templateUrl: './ur-popover-box-button.component.html',
    styleUrls: ['./ur-popover-box-button.component.scss'],
    imports: [URIconComponent],
})
export class URPopoverBoxButtonComponent {
    /**
     * The label describing the action of the button
     */
    @Input()
    label?: string;

    /**
     * Name of the icon to use
     */
    @Input()
    icon?: string;

    /**
     * click event listener
     */
    @Output()
    clicked: EventEmitter<Event> = new EventEmitter();
}
