import {
    AfterViewInit,
    ChangeDetectionStrategy,
    Component,
    contentChildren,
    effect,
    input,
    output,
    SimpleChanges,
    ViewEncapsulation,
} from '@angular/core';
import { URTabComponent } from '../Tab/ur-tab.component';

@Component({
    selector: 'ur-tabs',
    templateUrl: './ur-tabs.component.html',
    styleUrls: ['./ur-tabs.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    standalone: true,
})
export class URTabsComponent implements AfterViewInit {
    /**
     * Hold the list of URTabComponent that comes from the ngContent.
     * */
    tabsContent = contentChildren(URTabComponent);

    /**
     * Tab that should be active when component initializes
     */
    defaultIndex = input(0);

    /**
     * Whether to present the tabs vertically rather than horizontally
     * @deprecated
     */
    isVertical = input(false);

    hasBackground = input(false);

    /**
     * The link that has been selected in the template
     * */
    selectedLink = input('');

    /**
     * Link changed event emitter.
     * Should be used inside the main app to switch routes when tabs are clicked
     */
    linkChanged = output<string | undefined>();

    private _selectedIndex = 0;

    public get selectedIndex() {
        return this._selectedIndex;
    }

    constructor() {
        effect(() => {
            if (this.tabsContent()?.length > 0) {
                /**
                 * push the clicked element into the subscription stream
                 * */
                this.tabsContent()?.forEach((elem, index) => {
                    elem?.clicked?.subscribe(() => {
                        this.selectTabIndex(index);
                    });

                    // Watches for changes in link/title and updates as needed.
                    elem?.inputsChanged?.subscribe((changes: SimpleChanges) => {
                        const { link } = changes;
                        if (link && this.selectedIndex === index) {
                            this.selectTabIndex(index);
                        }
                    });
                });
            }
        });
    }

    ngAfterViewInit() {
        /**
         * Gives back the index from the selected tab
         * If nothing is being found it gives back -1
         * */
        const foundIdx = this.tabsContent().findIndex((item) => item.link()?.toLowerCase() === this.selectedLink().toLowerCase());

        /**
         * Check if the index was found or not, and update the ui accordingly
         * */
        if (foundIdx === -1) {
            this.selectTabIndex(this.defaultIndex());
        } else {
            this.selectTabIndex(foundIdx);
        }
    }

    /**
     * Select a new tab, moves underline, and emits linkChanged.
     */
    public selectTabIndex(index: number) {
        if (this.tabsContent()) {
            const targetComponent = this.tabsContent()[index];
            if (targetComponent) {
                const previousComponent = this.tabsContent()[this.selectedIndex];
                this._selectedIndex = index;
                /**
                 * Cleans up the previous selected tabs underline
                 * */
                previousComponent.setActiveState(false);
                /**
                 * Sets up the new underline in the selected tab
                 * */
                targetComponent.setActiveState(true);

                this.linkChanged.emit(targetComponent.link());
            }
        }
    }
}
