import { NgClass } from '@angular/common';
import { Component, Input, ViewEncapsulation } from '@angular/core';

type TitleType = 'title' | 'subtitle' | 'subheading' | 'small' | 'mini';
type Weight = 'default' | 'regular' | 'medium' | 'semi-bold' | 'bold';

/* @deprecated */
@Component({
    selector: 'ur-title',
    templateUrl: './ur-title.component.html',
    styleUrls: ['./ur-title.component.scss'],
    encapsulation: ViewEncapsulation.None,
    imports: [NgClass],
})
export class URTitleComponent {
    /**
     * title text
     */
    @Input()
    text = 'Title';

    /**
     * title types
     */
    @Input()
    type: TitleType = 'title';

    /**
     * weight of the font
     */
    @Input()
    weight: Weight = 'default';
}
