import { NgClass } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'ur-digital-signal-output-toggle',
    templateUrl: './ur-digital-signal-output-toggle.component.html',
    styleUrls: ['./ur-digital-signal-output-toggle.component.scss'],
    imports: [NgClass],
})
export class URDigitalSignalOutputToggleComponent {
    /**
     * Whether the value displayed is from a configurable Signal
     */
    @Input()
    type: 'configurable' | 'digital' = 'digital';

    /**
     * Whether the value is high or not
     */
    @Input()
    isHigh?: boolean;

    /**
     * The event emitted when the value is clicked
     */
    @Output()
    clicked = new EventEmitter();

    get classes(): string[] {
        return [
            this.isHigh ? 'ur-digital-signal-output-toggle-high' : 'ur-digital-signal-output-toggle-low',
            `ur-digital-signal-output-toggle-${this.type}`,
        ];
    }
}
