import { Overlay, OverlayConfig, OverlayRef } from '@angular/cdk/overlay';
import { BasePortalOutlet, ComponentPortal, ComponentType, TemplatePortal } from '@angular/cdk/portal';
import { Injectable, Injector, TemplateRef } from '@angular/core';
import { FocusTrap } from 'focus-trap';
import { Guid } from 'guid-typescript';
import { firstValueFrom } from 'rxjs';
import {
    CloseDialog,
    CloseReasonType,
    ConfirmationDialog,
    DIALOG_KEYS,
    DialogConfig,
    DialogError,
    LayerToken,
    PortalCloseEvent,
} from '@universal-robots/ui-models';
import { ANIMATION_STATE, ANIMATION_TRIGGER, DialogAnimationEvent } from '../animations/';
import { URServiceConfirmationDialogComponent } from '../components/Dialogs/ServiceConfirmationDialog/ur-service-confirmation-dialog.component';
import { CONFIG_TOKEN, DATA_TOKEN } from '../ref';
import { DialogRef } from '../ref';
import { LocalStorageList } from '../utils/local-storage';
import { URDialogContainerComponent } from './dialog/dialog-container.component';

const defaultConfig = {
    layer: '--layer-modal' as LayerToken,
    hasBackdrop: true,
    backdropClass: 'ur-overlay-backdrop',
    panelClass: 'ur-dialog-template-panel',
    disableClose: false,
};

@Injectable({ providedIn: 'root' })
export class URPortalService {
    localStorageHelper = new LocalStorageList<string>(DIALOG_KEYS.LOCALSTORAGE);

    constructor(private readonly overlay: Overlay) {
        if (!(window as any).__trapStack) {
            (window as any).__trapStack = new Array<FocusTrap>();
        }
        this.localStorageHelper.clear();
    }

    public open<T, C = any>(
        componentOrTemplate: ComponentType<any> | TemplateRef<any>,
        customData?: C,
        config?: DialogConfig,
    ): DialogRef<T> {
        (window as any).dispatchEvent(new CustomEvent<DialogAnimationEvent>(DIALOG_KEYS.OPEN_EVENT, {}));

        const dialogConfig: DialogConfig = { ...defaultConfig, ...config };
        const overlayRef = this.createOverlay(dialogConfig);
        const id = Guid.raw();
        overlayRef.hostElement.parentElement?.style.setProperty('--ur-portal-layer', `var(${dialogConfig.layer})`);
        const ref = new DialogRef<T>(overlayRef, id, dialogConfig);

        const dialogContainer = this.attachContainer(overlayRef, ref, customData, dialogConfig);
        this.attachContent<T, C | unknown>(componentOrTemplate, ref, dialogContainer, customData ?? {}, dialogConfig);

        this.localStorageHelper.add(id);
        // communicating with other portal services/instances
        (window as any).dispatchEvent(
            new CustomEvent<DialogAnimationEvent>(DIALOG_KEYS.UPDATE_EVENT, {
                detail: { animation: ANIMATION_TRIGGER.ENTER, state: ANIMATION_STATE.VISIBLE, activeDialogId: id, config: dialogConfig },
            }),
        );

        return ref;
    }

    /**
     * close the current dialog from portal - not recommended but possible
     * @param customData T
     */
    public close(customData: CloseDialog<unknown>) {
        // take last added dialog and close it

        const id = this.localStorageHelper.getLatest();
        if (id) {
            window.dispatchEvent(new CustomEvent<PortalCloseEvent>(DIALOG_KEYS.CLOSE_EVENT, { detail: { id, customData } }));
        }
    }

    /**
     * close the dialog based on provided id from portal
     * @param id The id for the dialog to close
     */
    public closeById(id: string) {
        if (id) {
            window.dispatchEvent(
                new CustomEvent<PortalCloseEvent>(DIALOG_KEYS.CLOSE_BY_ID_EVENT, { detail: { id, customData: { reason: 'DESTROYED' } } }),
            );
        }
    }

    /**
     * Closes dialogs based on the error type. This is used to close dialogs
     * @param error DialogError
     */
    public closeByErrorType(error: DialogError): Promise<string | undefined> {
        if (!error) {
            return Promise.resolve(undefined);
        }

        return new Promise<string | undefined>((resolve) => {
            const handler = (event: CustomEvent<DialogAnimationEvent>) => {
                const { detail } = event;
                if (detail.state === ANIMATION_STATE.HIDDEN && detail.activeDialogId !== this.localStorageHelper.getLatest()) {
                    resolve(detail.activeDialogId);
                } else {
                    resolve(undefined);
                }
            };
            (window as any).addEventListener(DIALOG_KEYS.UPDATE_EVENT, handler, { once: true });
            window.dispatchEvent(
                new CustomEvent<DialogError>(DIALOG_KEYS.CLOSE_BY_ERROR_TYPE_EVENT, {
                    detail: { ...error },
                }),
            );
        });
    }
    /**
    
     * Force closes all open dialogs if for some reason all overlays should be destroyed
     * @warning this will close all dialogs without any feedback - not recommended
     */
    public forceCloseAll() {
        // communicating with other portal services
        window.dispatchEvent(new CustomEvent<PortalCloseEvent>(DIALOG_KEYS.CLOSE_EVENT));
    }

    public async openRequiredConfirmationDialog(dialogContent: ConfirmationDialog): Promise<CloseReasonType> {
        return this.openConfirmation(dialogContent, true);
    }

    public async openConfirmationDialog(dialogContent: ConfirmationDialog): Promise<CloseReasonType> {
        return this.openConfirmation(dialogContent, false);
    }
    private async openConfirmation(dialogContent: ConfirmationDialog, disableClose: boolean): Promise<CloseReasonType> {
        const dialogRef = this.open<undefined, ConfirmationDialog>(URServiceConfirmationDialogComponent, dialogContent, { disableClose });
        const { reason } = await firstValueFrom(dialogRef.afterClose);
        return reason;
    }
    private attachContainer<T, CustomData = any>(
        overlayRef: OverlayRef,
        dialogRef: DialogRef<T>,
        customData: CustomData,
        config: DialogConfig,
    ) {
        const injector = this.createInjector(dialogRef, customData, config);
        const container = new ComponentPortal(URDialogContainerComponent, undefined, injector);
        const containerRef = overlayRef.attach(container);
        return containerRef.instance;
    }

    private createInjector<T, CustomData = any>(dialogRef: DialogRef<T>, customData: CustomData, config: DialogConfig): Injector {
        const providers = [
            {
                provide: DATA_TOKEN,
                useValue: customData,
            },
            {
                provide: CONFIG_TOKEN,
                useValue: config,
            },
            {
                provide: DialogRef,
                useValue: dialogRef,
            },
        ];
        return Injector.create({ parent: config.injector, providers });
    }
    private attachContent<T, C>(
        componentOrTemplateRef: ComponentType<any> | TemplateRef<any>,
        dialogRef: DialogRef<T>,
        dialogContainer: BasePortalOutlet,
        customData: C,
        config: DialogConfig,
    ) {
        const injector = this.createInjector(dialogRef, customData, config);
        if (componentOrTemplateRef instanceof TemplateRef && config.viewContainerRef) {
            dialogContainer.attachTemplatePortal(
                new TemplatePortal(componentOrTemplateRef, config.viewContainerRef, { $implicit: customData, dialogRef }, injector),
            );
        } else {
            dialogContainer.attachComponentPortal<C>(
                new ComponentPortal(componentOrTemplateRef as ComponentType<any>, config.viewContainerRef ?? undefined, injector),
            );
        }
    }

    private getOverlayConfig(config: DialogConfig): OverlayConfig {
        const positionStrategy = this.overlay.position().global().centerHorizontally().centerVertically();

        return new OverlayConfig({
            hasBackdrop: config.hasBackdrop,
            backdropClass: config.backdropClass,
            panelClass: config.panelClass,
            scrollStrategy: this.overlay.scrollStrategies.noop(),
            positionStrategy,
        });
    }

    private createOverlay(config: DialogConfig) {
        // Returns an OverlayConfig
        const overlayConfig = this.getOverlayConfig(config);

        // Returns an OverlayRef
        return this.overlay.create(overlayConfig);
    }
}
